import React, { useState, useEffect } from 'react'
import { toastr } from 'react-redux-toastr'
import { Link } from 'react-router-dom'
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import { servicesService } from '../../../_services/services.service'
import { history } from '../../../_helpers'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import elevatorTypes from '../../../_constants/elevatorTypes'
import Radio from '@material-ui/core/Radio';
import CreateFactorModal from './CreateFactorModal';
import Block from '@material-ui/icons/Block';
import DoneAll from '@material-ui/icons/DoneAll';
import LibraryBooks from '@material-ui/icons/LibraryBooks';
import Button from '@material-ui/core/Button';
import { FaPlus, FaSave } from 'react-icons/fa'
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { GoCalendar } from 'react-icons/go'
import { FaHashtag } from 'react-icons/fa'



// TabPanel.propTypes = {
//   children: PropTypes.node,
//   index: PropTypes.any.isRequired,
//   value: PropTypes.any.isRequired,
// };

const useStyles = makeStyles(theme => ({

  topbar: {
    background: "#67b65d",
    color: "#fff",
  },
  menuButton: {
    color: "#fff"
  }
}))




export const ServiceInfo = (props) => {
  const [service_id, setService_id] = React.useState(props.match.params.sid);
  const [tabValue, setTabValue] = React.useState(0);
  const [data, setData] = useState({})
  const [loading, setLoading] = useState(false)
  const [items, setItems] = React.useState([]);
  const [parts, setParts] = React.useState([]);
  const [showFactorDialog, setShowFactorDialog] = useState(false)
  const [denyReason, setDenayReason] = useState('')

  useEffect(() => {
    getData();
    setService_id(props.match.params.sid)
  }, [props.match.params.sid])

  const getData = () => {

    setLoading(true)
    servicesService.getById(props.match.params.sid)
      .then((res) => {
        console.log(res.data)

        let newData = res.data;

        setParts(newData.service.parts)
        setItems(newData.service.items)

        setData(newData);

        setLoading(false)

      })
      .catch((error) => {
        console.log(error);
        setLoading(false)
        toastr.info("مشکلی در دریافت اطلاعات بوجود آمد. لطفا دوباره سعی کنید.")
      });
  }

  const handleTabChange = (event, newValue) => {
    // setPage(0)
    // setData([])
    // setLoading(true)
    // setEndReached(false)
    setTabValue(newValue);
    // console.log(newValue);

  };

  const handleToggle = value => () => {
    const currentIndex = items.indexOf(value);
    const newItems = [...items];

    if (currentIndex === -1) {
      newItems.push(value);
    } else {
      newItems.splice(currentIndex, 1);
    }

    setItems(newItems);
  };

  const handleFactorModalShow = (val) => {
    setShowFactorDialog(val)
  }

  const handlePartsChange = (vals) => {

    setParts(vals)
    setShowFactorDialog(false)

  }

  const handleSubmit = () => {

    setLoading(true)

    let dto = {};
    dto["items"] = items;
    dto["parts"] = parts;
    dto["service_id"] = parseInt(service_id);

    console.log(JSON.stringify(dto))
    servicesService.complete_service(JSON.stringify(dto))
      .then((res) => {

        console.log(res.data)

        setLoading(false)

        history.push("/technician/services")

      })
      .catch((error) => {
        console.log(error);
        setLoading(false)
        toastr.info("مشکلی در دریافت اطلاعات بوجود آمد. لطفا دوباره سعی کنید.")
      });

  }

  const handleSubmitDenay = () => {

    setLoading(true)

    let dto = {};
    dto["deny_reason_id"] = denyReason;
    dto["service_id"] = parseInt(service_id);

    console.log(JSON.stringify(dto))
    servicesService.deny_service(JSON.stringify(dto))
      .then((res) => {

        console.log(res.data)

        setLoading(false)

        history.push("/technician/services")

      })
      .catch((error) => {
        console.log(error);
        setLoading(false)
        toastr.info("مشکلی در دریافت اطلاعات بوجود آمد. لطفا دوباره سعی کنید.")
      });

  }


  const classes = useStyles()

  return (
    <React.Fragment>
      <AppBar position="static" color="default" className={classes.appbar}>
        <Toolbar className={classes.topbar}>
          <IconButton className={classes.menuButton} color="default" aria-label="Menu">
            <Link to={"/technician/services/" + props.match.params.id}><NavigateBeforeIcon style={{ color: '#fff' }} /></Link>
          </IconButton>
          <Typography
            variant="inherit"
            color="inherit"
            className={classes.logo}
          // onClick={() => history.push("home")}
          >
            {/* <img className={styles.imgLogo} src={logo} alt="Logo" />{" "} */}
            {/* <span className={styles.txtLogo}>{appconf('NAME')}</span> */}
          </Typography>
          <Typography
            variant="inherit"
            color="inherit"
            className="appbar-title">
            جزئیات سرویس
          </Typography>
        </Toolbar>
      </AppBar>
      <div className="orders projects">

        <Tabs
          value={tabValue}
          onChange={handleTabChange}
          variant="fullWidth"
          indicatorColor="primary"
          textColor="primary"
          aria-label="icon label tabs"
          style={{ marginBottom: 5 }}
        >
          <Tab icon={<LibraryBooks />} value={0} label="جزئیات سرویس" />
          <Tab icon={<DoneAll />} value={1} label="انجام سرویس" />
          <Tab icon={<Block />} value={2} label="رد کردن سرویس" />
        </Tabs>

        <div className={tabValue === 0 ? 'show' : 'hidden'} style={{ height: 'calc(100vh - 210px)' }}>
          {data && data.service &&
            <div className="service-full-info">
              <div className="first-row">
                <span>
                  <FaHashtag size="16px" />
                  <span>{data.service.contract.contract_number}</span>
                </span>
                <span> </span>
                <span>
                  <GoCalendar size="16px" />
                  <span>{data.service.created_at}</span>
                </span>
              </div>

              <div className="row">
                <div className="col-xs-12 pd1030">
                  آدرس سرویس:  {data.service.contract.address}
                </div>
              </div>

              <div className="row">
                <div className="col-xs-12 pd1030">
                  نام مشتری:  {data.service.customer.full_name}
                </div>
              </div>

              <div className="row">
                <div className="col-xs-12 pd1030">
                  موبایل مشتری:  {data.service.customer.cell}
                </div>
              </div>

              <div className="row">
                <div className="col-xs-12 pd1030">
                  تعداد طبقات:  {data.service.contract.floors}
                </div>
              </div>

              <div className="row">
                <div className="col-xs-12 pd1030">
                  تعداد توقف:  {data.service.contract.stops}
                </div>
              </div>

              <div className="row">
                <div className="col-xs-12 pd1030">
                  نوع آسانسور:  {data.service.contract.elevator_type ? elevatorTypes.filter(el => el.value === parseInt(data.service.contract.elevator_type))[0].text : ''}
                </div>
              </div>

              <div className="row">
                <div className="col-xs-12 pd1030">
                  تعداد درب لولایی:  {data.service.contract.swing}
                </div>
              </div>

              <div className="row">
                <div className="col-xs-12 pd1030">
                  تعداد درب اتوماتیک:  {data.service.contract.automatic}
                </div>
              </div>

              <div className="last-row">
                <span>نوع سرویس: {data.request_type === 0 ? 'ماهانه' : 'خرابی'}</span>
                <span>
                </span>
              </div>
            </div>
          }
        </div>
        <div className={tabValue === 1 ? 'show' : 'hidden'} style={{ height: 'calc(100vh - 210px)' }}>

          <div className="row">
            <div className="col-xs-12">
              <List style={{ maxWidth: '95%' }}
                fullWidth>
                {data && data.items && data.items.map(value => {
                  const labelId = `checkbox-list-label-${value.id}`;

                  return (
                    <ListItem key={value.id} role={undefined} dense button onClick={handleToggle(value.id)} >
                      <ListItemIcon>
                        <Checkbox
                          edge="start"
                          color="primary"
                          checked={items.indexOf(value.id) !== -1}
                          tabIndex={-1}
                          disableRipple
                          inputProps={{ 'aria-labelledby': labelId }}
                        />
                      </ListItemIcon>
                      <ListItemText id={labelId} primary={value.name} />

                    </ListItem>
                  );
                })}
              </List>
            </div>

          </div>
          <div className="row" style={{ marginBottom: 10 }}>

            <div className="col-xs-12 ltr">
              <Button
                fullWidth={true}
                variant="contained"
                color="primary"
                size="large"
                // className={classes.button}
                endIcon={<FaPlus />}
                onClick={() => handleFactorModalShow(true)}
              >فاکتور قطعات و اجرت</Button>
            </div>

          </div>
          <div className="row" style={{ marginBottom: 20 }}>

            <div className="col-xs-12 ltr">
              <Button
                fullWidth={true}
                variant="contained"
                color="primary"
                size="large"
                onClick={() => handleSubmit()}
                // className={classes.button}
                endIcon={<FaSave />}
              >ثبت اطلاعات</Button>
            </div>

          </div>
        </div>
        {data && data.service && data.service.status < 2 ?
          <div className={tabValue === 2 ? 'show' : 'hidden'} style={{ height: 'calc(100vh - 210px)' }}>

            <div className="row">
              <div className="col-xs-12">
                <List style={{ maxWidth: '95%' }}
                  fullWidth>
                  {data && data.reasons && data.reasons.map(value => {
                    const labelId = `checkbox-list-label-${value.id}`;

                    return (
                      <ListItem key={value.id} role={undefined} dense button onClick={() => setDenayReason(value.id)}>
                        <ListItemIcon>
                          <Radio
                            edge="start"
                            color="primary"
                            checked={value.id === denyReason}
                            tabIndex={-1}
                            disableRipple
                            inputProps={{ 'aria-labelledby': labelId }}
                          />
                        </ListItemIcon>
                        <ListItemText id={labelId} primary={value.name} />

                      </ListItem>
                    );
                  })}
                </List>
              </div>

            </div>
            <div className="row" style={{ marginBottom: 20 }}>

              <div className="col-xs-12 ltr">
                <Button
                  disabled={denyReason === undefined}
                  fullWidth={true}
                  variant="contained"
                  color="primary"
                  size="large"
                  onClick={() => handleSubmitDenay()}
                  // className={classes.button}
                  endIcon={<FaSave />}
                >انصراف از سرویس</Button>
              </div>

            </div>

          </div>
          : ''}

      </div>



      <CreateFactorModal data={parts} open={showFactorDialog} onClose={() => handleFactorModalShow(false)} onChangeAndClose={(vals) => handlePartsChange(vals)} />

    </React.Fragment >
  );

}
