/*eslint-env jquery*/
import React, { Component } from 'react'
import { toastr } from 'react-redux-toastr'
import Loading from '../../Shared/Loading'
import { Tabs, Tab } from 'react-bootstrap'
import { history } from '../../../_helpers'
import { usersService } from '../../../_services/users.service'
import { userService } from '../../../_services/user.service'
import { Formik } from 'formik'
import * as Yup from 'yup'
import Step1 from './Step1'
import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai";

class Form extends Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      loading: false,
      key: 'info',
      id: this.props.match.params.id,
      data: {
        // gender: '',
        first_name: '',
        last_name: '',
        username: '',
        role: '',
        // email: '',
        password: '',
        // confirmPassword: ''
      }
    }


  }

  componentDidMount() {
    this.getData()
  }

  getData = async () => {

    const { id } = this.state;

    if (id) {

      this.setState({ loading: true });

      await usersService.getById(id)
        .then((res) => {
          console.log(res.data);
          let data = res.data;
          data["password"] = null;
          this.setState({ data: data, loading: false })

        })
        .catch((error) => {

          console.log(error);

          this.setState({ loading: false });

        });

    }


  };

  handleSubmit = (values, setSubmitting) => {
    // this.setState({ loading: true });
    // console.log(values);
    console.log(JSON.stringify(values));

    if (values.id) {

      if (values.password === null || values.password === ''){
        values["password"] = undefined;
      }

      userService.edit(values.id, JSON.stringify(values))
        .then((res) => {

          toastr.success('کاربر "' + values.username + '" با موفقیت ویرایش شد');
          history.push("/admin/users")
          setSubmitting(false);

        })
        .catch((error) => {
          console.log(error);

          setSubmitting(false);
        });

    } else {

      usersService.add(JSON.stringify(values))
        .then((res) => {
          console.log(res);

          this.setState({ loading: false, data: res.data });
          toastr.success('کاربر جدید  "' + values.username + '" با موفقیت ثبت شد');
          history.push("/admin/users")

          setSubmitting(false);


        })
        .catch((error) => {
          console.log(error);
          setSubmitting(false);
        });

    }
  }

  handleError(error) {
    this.setState({ loading: false });
    // toastr.warning('مشکلی پیش آمد', error);
  }

  validateForm = (errors) => {
    if (Object.keys(errors).length > 0) {
      // console.log(errors)
      let obj = errors
      let { createdDate, workStartDate, firstName, lastName, codeMeli, idnumber, birthDate, cellNumber, address, travelBrandId, gender, degree, childrenCount,
        driverDocuments,
        user
      } = obj;
      if (createdDate || workStartDate || firstName || lastName || codeMeli || idnumber || birthDate || cellNumber || address || travelBrandId || gender || degree || childrenCount) {
        this.setState({ key: "info" })
      } else if (driverDocuments) {
        this.setState({ key: "images" })
      }
    }
  }

  render() {
    const { data } = this.state;
    const ValidationSchema = Yup.object().shape({
      // gender: Yup.string().trim().nullable().required('جنسیت'),
      role: Yup.string().trim().nullable().required('نوع کاربر'),
      first_name: Yup.string().trim().nullable().required('نام'),
      last_name: Yup.string().trim().nullable().required('نام خانوادگی'),
      username: Yup.string().trim().nullable().required('نام کاربری'),

      password: data.id ? '' : Yup.string().trim().nullable().required('کلمه عبور'),

      // userName: Yup.string().trim().required('نام کاربری'),
      // email: Yup.string().email('صحیح ایمیل').trim().required('ایمیل'),

      // changePass: Yup.bool(),
      // password: Yup
      //   .string()
      //   .when("changePass", {
      //     is: true,
      //     then: Yup.string().matches(/^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=.*[~!@#$%^&*()-_+=/\|?.><])(?=\S+$).{8,}$/, { message: "کلمه عبور باید شامل موارد زیر باشد", excludeEmptyString: false })
      //       .min(8, 'طول کلمه عبور حداقل باید 8 کاراکتر باشد')
      //       .trim().required('وارد کردن کلمه عبور الزامی است'),
      //   }),

      // // changePass: Yup.bool(),
      // confirmPassword: Yup
      //   .string()
      //   .when("changePass", {
      //     is: true,
      //     then: Yup.string()
      //       .oneOf([Yup.ref('password')], 'تکرار کلمه عبور صحیح نیست')
      //       .trim().required('تکرار کلمه عبور الزامی است'),
      //   }),


    });
    return (
      <Formik
        enableReinitialize
        initialValues={data}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(true);
          this.handleSubmit(values, setSubmitting);
        }}
        validationSchema={ValidationSchema}
      >
        {props => {
          const {
            values,
            touched,
            errors,
            setFieldValue,
            setFieldTouched,
            isSubmitting,
            handleChange,
            handleBlur,
            handleSubmit,
            validateForm,
          } = props;
          return (
            <div>
              <div className="row" style={{ position: "relative" }}>
                <form onSubmit={handleSubmit}>
                  <button type="submit" className={"icon button submit-btn " + (isSubmitting ? "isSubmitting" : "")} disabled={isSubmitting}
                    onClick={() =>
                      validateForm().then(errors => {
                        this.validateForm(errors);
                      })
                    } >
                    {isSubmitting ?
                      <AiOutlineLoading className="mdi-spin" size="22px" />
                      :
                      <AiOutlineSave size="22px" />

                    }
                    ذخیره
                  </button>
                  <Tabs transition={false}
                    id="controlled-tab-example"
                    activeKey={this.state.key}
                    onSelect={key => this.setState({ key })}
                  >

                    <Tab eventKey="info" title="اطلاعات کاربر">

                      <Step1 {...props} />

                    </Tab>

                  </Tabs>
                </form>
              </div>
              {this.state.loading ? <Loading /> : ''}
            </div>
          );
        }}
      </Formik>
    );
  }
}

export default Form;
