import React, { Component } from 'react'
import { servicesService } from '../../../_services/services.service'
import { AiOutlineLoading } from "react-icons/ai"
import ServiceDetailsModal from './ServiceDetailsModal';
import { Virtuoso } from 'react-virtuoso'
import List from '@material-ui/core/List'
import Item from './Item'


export class ServicesList extends Component {

	constructor(props, context) {
		super(props, context);

		this.state = {
			initLoading: true,
			loading: false,
			data: [],
			page: 1,
			page_size: 10,
			status: this.props.status,
			total: 0,
			endReached: false,
			showDialog: false,
			selectedContractId: '',
			reasons: [],
      serviceDetailsModalShow: false,
      reloadList: false,
		}

	}

	// const[data, setData] = useState([])
	// const[page, setPage] = useState(0)
	// const[page_size, setPageSize] = useState(4)
	// const[total, setTotal] = useState(0)
	// const items = useRef([])
	// const[loading, setLoading] = useState(false)
	// const[initLoading, setInitLoading] = useState(true)
	// const[endReached, setEndReached] = useState(false)

	// const classes = useStyles()
	// the setTimeout below simulates a network request.
	// In the real world, you can fetch data from a service.
	// the setTotal call will trigger a refresh for the list,
	// so make sure you call it last.

	// componentWillReceiveProps(nextProps) {

	//     if (nextProps.projectStatus !== undefined && nextProps.projectStatus !== this.state.projectStatus) {
	//         this.setState({ projectStatus: nextProps.projectStatus, page: 0, data: [] }, () => {
	//             this.loadMore()
	//         })

	//     }

	// }

	componentDidMount() {
		this.loadMore()
	}

	loadMore = () => {

		const { data, page, page_size, projectStatus, endReached } = this.state;
		// console.log(data, page, page_size, projectStatus, endReached);

		// setTimeout(() => {
		if (!endReached) {
			// setLoading(true)
			this.setState({ loading: true });

			const dto = {
				page: page,
				page_size: page_size,
				contract_id: this.props.contract_id,
				status: this.props.status,
			};
			// console.log(JSON.stringify(dto));


			// setPage(page + 1)

			servicesService.getAllByUser(dto)
				.then((res) => {
					console.log(res.data)

					let newData = data.concat(res.data.data);
					// setData(newData);

					let total = res.data.total_records;
					let endReached = (newData.length === total) ? true : false;
					this.setState({ data: newData, total: newData.length, endReached: endReached, page: page + 1, loading: false, initLoading: false })
					// console.log(newData);

					// console.log(total, newData.length);

					// setTotal(newData.length)

					// if (total === newData.length) {
					// 	// setEndReached(true)
					// }

					// setLoading(false)
					// setInitLoading(false)

				})
				.catch((error) => {
					console.log(error);
					this.setState({ loading: false, initLoading: false });
				});

		}
		// }, 100)
		// setTimeout(() => {
		//   for (let index = total; index < total + total + 20; index++) {
		//     items.current = [...items.current, getUser(index)]
		//   }
		//   setLoading(false)
		//   setTotal(items.current.length)
		// }, 500)
	}

	// useEffect(() => {
	//     console.log("here");

	//     setData('')

	//     console.log(data);

	//     loadMore()
	// }, [props.projectStatus])


	// const loadMore = () => {

	//   const { dto } = this.state;

	//   contractsService.getAll(JSON.stringify(dto))
	//     .then((res) => {
	//       console.log(res.data)
	//       setData(res.data.data)
	//     })
	//     .catch((error) => {
	//       console.log(error);
	//     });
  // };
  
  componentWillReceiveProps(nextProps){
    if (nextProps && nextProps.reloadList !== this.state.reloadList) {
      this.setState({reloadList: false}, () => {
        this.loadMore()
      })
    }
  }

	handleServiceDetailsModalShow = (val, data) => {
		this.setState({serviceDetailsModalShow: val, serviceDetails: data})
	}

	handleCloseAndReload = () => {
		this.setState({serviceDetailsModalShow: false, data: [], page: 1, page_size: 10, endReached: false}, () => {
			this.loadMore()
		})
	}

	render() {
		const { data, total, loading, endReached, initLoading, showDialog, selectedContractId, reasons } = this.state;
		return (
			<>
				{initLoading ?

					<div className="projects-init-loading">
						<AiOutlineLoading className="mdi-spin" size="32px" />
					</div>

					:

					data.length > 0 ?

						<Virtuoso
            
							style={{ width: '100%', height: '100%' }}
							totalCount={total}
							item={(index) => {
								return (
									<Item data={data[index]} key={index} onItemClick={(data_) => data[index].status === 1 ? this.handleServiceDetailsModalShow(true, data_): null} />
								)
							}}
							endReached={() => this.loadMore()}
							ListContainer={({ listRef, className, style, children }) => {
								return (
									<List
										ref={listRef}
										style={style}
										
									>
										{children}
									</List>
								)
							}}
							// ItemContainer={({ children, ...props }) => {
							//   return (
							//     <ListItem button {...props} style={{ margin: 0 }}>
							//       {children}
							//     </ListItem>
							//   )
							// }}
							footer={() => {
								return !endReached && (
									<div className="list-loading-more">
										{/* <button disabled={loading} onClick={loadMore}> */}
										{loading ? 'در حال بارگذاری، لطفا صبر کنید ...' : ''}
										{/* </button> */}
									</div>
								)
							}}
						/>
						:

						<div className="projects-init-loading">موردی برای نمایش یافت نشد</div>
				}
				<ServiceDetailsModal data={this.state.serviceDetails} open={this.state.serviceDetailsModalShow} 
				onCloseAndReload={() => this.handleCloseAndReload()}
				onClose={() => this.handleServiceDetailsModalShow(false, undefined)} />
			</>

		);
	}
}
