import axios from 'axios';
import { handleSuccess, handleError } from './axiosHandler';
import { authHeader, authHeaderJson } from '../_helpers/auth-header';

import { config } from '../config';
const base_url = config.apiUrl;

const model = 'users/';

export const usersService = {
  getById,
  add,
  activate,
  checkStatus,
  logout,
};

function getById(id) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function checkStatus() {
  const url = base_url + model + 'checkStatus';
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function add(dto) {
  const url = base_url + model;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: { 'Content-Type': 'application/json; charset=utf-8' } });
}

