async function activate(dto) {

  const url = base_url + model + 'activate';

  try {
    const response = await axios.post(url, dto, { headers: { 'Content-Type': 'application/json' } });

    // setTimeout(() => {
      // localStorage.setItem('user', JSON.stringify(response.data));
    // }, 2000);

    return response;
  }
  catch (error) {
    handleError(error);
  }
}


function logout() {
  // remove user from local storage to log user out
  localStorage.removeItem('user');
  localStorage.removeItem('token');
}
