import React from "react"
import { Field, ErrorMessage, getIn } from "formik"
import ErrorPopUp from '../../Shared/ErrorPopUp'
import GenderSelect from '../../Shared/GenderSelect'
import RoleSelect from '../../Shared/RoleSelect'


export default ({ touched, errors, setFieldValue, values, handleBlur }) => (
  <div className="row display-flex">
    <div className="col-lg-8 col-lg-offset-2 col-md-10 col-md-offset-1 col-sm-12 col-xs-12">

      <div className="row">
        <div className="form-inline">

          {/* <div className="col-lg-3 col-sm-12">
            <label></label>
            <Field
              component={FormikCheckbox}
              name="status"
              id="status"
              label={"کاربر فعال است"}
            />
          </div>
          <div className="clearfix form-row"></div> */}

          <div className={"col-lg-4 col-sm-12 " + (touched.role && errors.role && "has-error")}>
            <label>نوع کاربر</label>
            <RoleSelect name="role" value={values.role} defaultValue={values.role}
              onChange={(val) => {
                setFieldValue('role', val);
              }}

            />
            <ErrorPopUp error={errors.role} touched={touched.role} type="select" />
          </div>

          <div className="col-lg-4 col-sm-12">
            <label>نام کاربری</label>
            <Field type="text"

              name="username"
              className={getIn(touched, "username") ? getIn(errors, "username") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorPopUp error={errors.username} touched={touched.username} type="fullText" />
          </div>

          <div className="col-lg-4 col-sm-12">
            <label>کلمه عبور</label>
            <Field type="password"
              autoComplete="new-password"
              name="password"
              className={getIn(touched, "password") ? getIn(errors, "password") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name="password" />
          </div>

          <div className="clearfix form-row"></div>

          <div className={"col-lg-3 col-sm-12 " + (touched.gender && errors.gender && "has-error")}>
            <label>جنسیت</label>
            <GenderSelect name="gender" value={values.gender} defaultValue={values.gender}
              onChange={(val) => {
                setFieldValue('gender', val);
              }}

            />
            <ErrorPopUp error={errors.gender} touched={touched.gender} type="select" />
          </div>

          <div className="col-lg-3 col-sm-12">
            <label>نام</label>
            <Field type="text"

              name="first_name"
              className={getIn(touched, "first_name") ? getIn(errors, "first_name") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name="first_name" />
          </div>

          <div className="col-lg-3 col-sm-12">
            <label>نام خانوادگی</label>
            <Field type="text"

              name="last_name"
              className={getIn(touched, "last_name") ? getIn(errors, "last_name") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name="last_name" />
          </div>

          <div className="col-lg-3 col-sm-12">
            <label>تلفن</label>
            <Field type="text"

              name="cell"
              className={getIn(touched, "cell") ? getIn(errors, "cell") ? "text-input" : "text-input" : "text-input"}
            />
            <ErrorMessage component={ErrorPopUp} name="cell" />
          </div>

          <div className="clearfix form-row"></div>

          <div className="col-lg-12 col-sm-12">
            <label>توکن firebase</label>
            <Field type="text"
              name="firebase_token"
              className={getIn(touched, "firebase_token") ? getIn(errors, "firebase_token") ? "text-input" : "text-input" : "text-input"}
            />
            <ErrorMessage component={ErrorPopUp} name="firebase_token" />
          </div>

          <div className="clearfix form-row"></div>


        </div>
      </div>

    </div>

  </div>
);