import React, { Component } from 'react'

function days() {
  let days_ = [];
  for (let i = 1; i < 31; i++) {
    let day = { text: i, value: i };
    days_.push(day);
  }
  return days_;
}


class ServiceDaySelect extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      defaultValue: this.props.defaultValue,
      data: days(),
    };
  }

  // componentWillMount() {
  //   this.getData();
  // }

  // getData() {

  //   categoriesService.getAll()
  //     .then((res) => {
  //       let data = res.data.map(elm => ({ "text": elm.name, "value": elm.id }))
  //       this.setState({ data: data, loading: false, isSubmitting: false });
  //     }).catch((error) => { console.log(error) });

  // };


  async componentWillReceiveProps(nextProps) {
    if (nextProps.defaultValue !== this.state.defaultValue) {
      this.setState({ defaultValue: nextProps.defaultValue });
    }
  }

  handleChange = (e) => {

    let selected = e.target.value;
    this.setState({ selected: selected })
    this.props.onChange(selected)

  }

  render() {

    const { data, loading, defaultValue, selected } = this.state;
    const { clearable, className } = this.props;

    return (
      <input type="text"
        className={className}
        defaultValue={defaultValue}
        onChange={(e) => this.handleChange(e)}
      />
     
    );
  }
}
export default ServiceDaySelect;
