import React, { useState } from 'react'
import { toastr } from 'react-redux-toastr'
import { connect } from 'react-redux'
import { Row, Col } from 'react-bootstrap'
import { userActions } from '../../_actions'
import { AiOutlineLoading } from "react-icons/ai";
import loginImg from '../../assets/images/login-w-icon.png'


const Login = (props) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [submitting, setSubmitting] = useState(false);

  const { dispatch } = props;

  const handleSubmit = () => {

    if (username && password) {

      setSubmitting(true)

      let dto = {};
      dto["username"] = username;
      dto["password"] = password;

      dispatch(userActions.login(dto));

    } else {
      toastr.warning("لطفا اطلاعات کاربری خود را وارد نمایید")
    }


  }
  
  const _handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      handleSubmit()
    }
  }

  return (
    <div className="login-container">
      <ResponsiveContainer>
        <div className="login">
          <div className="logo-container">
            <div className="login-image">
              <img src={loginImg} alt="" />
            </div>
          </div>
          <div className="content">

            <input type="text" className={"text-input"} autoComplete="off" value={username} maxLength="15"
              placeholder="نام کاربری خود را وارد نمایید" onChange={e => setUsername(e.target.value)}
            />
            <input type="text" className={"text-input"} autoComplete="off" value={password} maxLength="15"
             onKeyDown={_handleKeyDown}
              placeholder="کلمه عبور خود را وارد نمایید" onChange={e => setPassword(e.target.value)}
            />
            <a onClick={() => handleSubmit()}>
              {submitting ?
                <><AiOutlineLoading className="mdi-spin" size="22px" style={{ marginLeft: 5 }} /> لطفا منتظر بمانید ... </>
                :
                'ورود به سیستم'
              }
            </a>
          </div>
        </div>
      </ResponsiveContainer>
    </div >
  )
}

const ResponsiveContainer = ({ children }) => (
  <Row className="justify-content-md-center">
    <Col lg={4} md={4} sm={12} xs={12} className="col-lg-offset-4 col-md-offset-4">

      {children}

    </Col>
  </Row>
)




function mapStateToProps(state) {
  const { loggingIn, loggedIn } = state.authentication;
  return {
    loggingIn,
    loggedIn
  };
}

const connectedLogin = connect(mapStateToProps)(Login);
export { connectedLogin as Login };