import React, { Component } from 'react'
import ReactSelect from 'react-select'
import SimpleValue from 'react-select-simple-value'

export const Select = ({ options, value, ...props }) => (
  <SimpleValue options={options} value={value}>
    {simpleProps => <ReactSelect {...props} {...simpleProps} />}
  </SimpleValue>
);

const types = [
  {label: "قراردادهای رو اتمام", value: "due_contracts"},
  {label: "بیمه رو به اتمام", value: "due_insurances"},
  {label: "استاندارد رو به اتمام", value: "due_standards"},
  {label: "بدون بیمه", value: "no_insurances"},
  {label: "بدون استاندارد", value: "no_standards"},
]

class DashboardContractsTypeSelect extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      defaultValue: '',
      data: types,
    };
  }

  // componentWillMount() {
  //   this.getData();
  // }

  // getData() {
  //   provincesService.getAll()
  //     .then((res) => {
  //       // console.log(res.data)
  //       let data = res.data.map(elm => ({ "label": elm.name, "value": elm.id }))
  //       this.setState({ data: data, loading: false, isSubmitting: false });
  //     })
  // };

  handleChange = e => {
    // console.log(e);

    this.props.onChange(e);
  }

  render() {
    const customStyles = {
      // option: (provided, state) => ({
      //   ...provided,
      //
      // }),
      control: (provided) => ({
        // none of react-select's styles are passed to <Control />
        ...provided,
        background: '#f6f6f6',
        borderRadius: 20,
        // height: 40,
        boxShadow: 'none',
        border: 'none',
        paddingRight: '12',
      }),
      indicatorSeparator: () => ({
        display: 'none'
      }),

    }
    const { loading, data } = this.state;
    const { disabled, className } = this.props;

    return (
      <Select
        isDisabled={disabled}
        placeholder=""
        styles={customStyles}
        options={data}
        onChange={this.handleChange}
        value={this.props.defaultValue}
        className={className ? className : "select"}
        isLoading={loading}
        noOptionsMessage={() => "نتیجه ای یافت نشد"}
        loadingMessage={() => "در حال دریافت اطلاعات از سرور"}
        value={this.props.defaultValue}
      />
    );
  }
}
export default DashboardContractsTypeSelect;
