import React from "react"
import Map from '../../Shared/Map'


export default ({ touched, errors, setFieldValue, values, handleBlur, tabChanged }) => (
  <div className="row display-flex">
    <div className="col-lg-8 col-lg-offset-2 col-md-12 col-sm-12 col-xs-12">

      <div className="row">
        <div className="form-inline">

          <div className="col-md-12">
            <label></label>
            <Map zoom={12}
              tabChanged={tabChanged}
              onChange={(val) => {
                setFieldValue('lat', val.lat.toFixed(6));
                setFieldValue('lng', val.lng.toFixed(6));
              }}
              center={values.lat && values.lng ? {
                lat: values.lat > 0 ? values.lat : 38.079987,
                lng: values.lng > 0 ? values.lng : 46.296130
              }
                : { lat: 38.079987, lng: 46.296130 }}
            />
            {
              (errors.lat || errors.lng) &&
              <div className="required_validator visible"><div className="validator_container"><span>{'وارد کردن [مختصات محل] الزامی است'}</span></div></div>
            }
          </div>


        </div>
      </div>

    </div>

  </div>
);