import React, { Component } from "react"
import { Map, TileLayer, Marker } from "react-leaflet"
import L from 'leaflet'

const MyMarker = props => {

  const initMarker = ref => {
    if (ref) {
      ref.leafletElement.openPopup()
    }
  }

  return <Marker ref={initMarker} {...props} />
}

class MapExample extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPos: {}
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    this.setState({ currentPos: e.latlng });

    this.props.onChange(e.latlng);
    // const { lat, lng } = e.latlng;
    // console.log(e.latlng);
  }

  handleMove = (e) => {
    var map = e.target;
    console.log(map.getCenter());

  }

  handleDragend = (e) => {

    var map = e.target;
    console.log(e);


  }

  componentWillReceiveProps(nextProps) {

    if (nextProps && nextProps.tabChanged) {
      setTimeout(() => {
        this.mapRef.leafletElement.invalidateSize(true)
      }, 100);
      
      
    }



  }

  render() {

    const myIcon = L.icon({
      iconUrl: require('../../assets/images/marker-icon.png'),
      iconSize: [42, 64],
      iconAnchor: [21, 64],
      popupAnchor: null,
      shadowUrl: null,
      shadowSize: null,
      shadowAnchor: null
    });
    return (
      <Map center={this.props.center} zoom={this.props.zoom} onClick={this.handleClick}
        //  onMoveEnd={e => this.handleMove(e)} onDragend={e => this.handleDragend(e)} 
        ref={(ref) => this.mapRef = ref} id="mapId"
      >
        <TileLayer
          url='https://{s}.tile.osm.org/{z}/{x}/{y}.png'
        />
        {this.props.center && <MyMarker icon={myIcon} position={this.props.center} setView={true}>

        </MyMarker>}
        {/* <GeoSearch /> */}
      </Map>
    )
  }
}

export default MapExample;
