import React, { Component } from 'react'
import Chart from 'react-apexcharts'

class CircleProgressBar2 extends Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      loading: true,
      options: {
        chart: {
          toolbar: {
            show: false
          }
        },
        plotOptions: {
          radialBar: {
            startAngle: -135,
            endAngle: 225,
            hollow: {
              margin: 0,
              size: '70%',
              background: '#fff',
              position: 'front',
              dropShadow: {
                enabled: true,
                top: 3,
                left: 0,
                blur: 4,
                opacity: 0.24
              }
            },
            track: {
              background: '#fff',
              strokeWidth: '67%',
              margin: 0, // margin is in pixels
              dropShadow: {
                enabled: true,
                top: -3,
                left: 0,
                blur: 4,
                opacity: 0.35
              }
            },
            dataLabels: {
              name: {
                offsetY: -10,
                show: true,
                color: '#888',
                fontSize: '17px'
              },
              value: {
                formatter: () => this.props.score,
                color: '#111',
                fontSize: '36px',
                show: true,
              }
            }
          }
        },
        fill: {
          type: 'gradient',
          gradient: {
            shade: 'dark',
            type: 'horizontal',
            shadeIntensity: 0.5,
            gradientToColors: ['#4a148c'],
            inverseColors: false,
            opacityFrom: 1,
            opacityTo: 1,
            stops: [0, 100]
          }
        },
        stroke: {
          lineCap: 'round'
        },
        labels: ['سرویسهای فعال'],
      },
      series: [0],
      score: 0,
    }


  }

  componentWillMount() {

    let score = this.props.score;

    if (score <= 100) {
      score = score
    } else if (score <= 1000) {
      score = (score * 100) / 1000
    } else if (score <= 2000) {
      score = (score * 100) / 2000
    }

    this.setState({ score: score })

  }

  render() {
    const { options, score } = this.state;
    return (
      <div className="radialBar">
        <Chart options={options} series={[score]} type="radialBar" width="280" />
      </div>
    );
  }
}

export default CircleProgressBar2;
