import React, { useState, useEffect } from 'react'
import NumberFormat from 'react-number-format'
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Dialog from '@material-ui/core/Dialog';
import Divider from '@material-ui/core/Divider';
import DoneAll from '@material-ui/icons/DoneAll';


const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    direction: 'rtl',
    textAlign: 'right',

  },
  inline: {
    display: 'inline',
  },
}));

function NumberFormatCustom(props) {
  const { inputRef, onChange, ...other } = props;

  return (
    <NumberFormat
      {...other}
      getInputRef={inputRef}
      onValueChange={values => {
        onChange({
          target: {
            value: values.value,
          },
        });
      }}
      thousandSeparator
      isNumericString
    // prefix="$"
    />
  );
}

NumberFormatCustom.propTypes = {
  inputRef: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
};

const ServiceDetailsModal = (props) => {
  const { data, onClose, open, ...other } = props;
  const [parts, setParts] = useState(data && data.parts ? data.parts : [{ name: '', qty: 1, price: '', totalPrice: '' }])
  const classes = useStyles();
  useEffect(() => {
    props.data && props.data.parts && setParts(props.data.parts)
  }, [props.data])

  const handleAddRow = () => {

    var newParts = [...parts];
    var newPart = { name: '', qty: 1, price: '', totalPrice: '' };
    newParts.push(newPart);
    setParts(newParts);

  }

  const handleClose = () => {
    onClose()
  };

  return (
    <Dialog
      fullScreen={true}
      onClose={handleClose}
      // onEntering={handleEntering}
      aria-labelledby="confirmation-dialog-title"
      open={open}
      {...other}
    >
      <DialogTitle id="confirmation-dialog-title">جزئیات سرویس انجام شده</DialogTitle>
      <DialogContent dividers>
        <List className={classes.root}>
          {data && data.service_items.map((item, index) => (
            <React.Fragment key={item.id}>

              <ListItem alignItems="flex-end" className="checklist-item" >
                <DoneAll className="doneAll-icon" />
                <ListItemText primary={item.name} />
              </ListItem>
              <Divider component="li" />
            </React.Fragment>
          ))}
        </List>


        <br />
        {parts ?
          <>
            <table className="table-parts" style={{ width: '100%' }}>
              <thead>
                <tr>
                  <th>ردیف</th>
                  <th>نام کالا / خدمات</th>
                  <th>تعداد</th>
                  <th>فی</th>
                  <th>جمع</th>
                </tr>
              </thead>
              <tbody>
                {
                  parts.map((part, index) => (
                    <tr key={"part-" + index}>
                      <td>{index + 1}</td>
                      <td>
                        {part.name}
                      </td>
                      <td>
                        {part.qty}
                      </td>
                      <td>
                        {part.price}
                      </td>
                      <td>
                        {part.qty * part.price}
                      </td>
                    </tr>
                  ))
                }
              </tbody>
            </table>
            <hr />

            <div className="price-items">
              <span>جمع قطعات و اجرت: </span>
              <span>{data && data.part_price}</span>
            </div>
          </>
          : ''}
        <div className="price-items">
          <span>مبلغ سرویس: </span>
          <span>{data && data.service_price}</span>
        </div>
        <div className="price-items">
          <span>مبلغ قابل پرداخت: </span>
          <span><NumberFormat thousandSeparator={true} displayType="text" value={data && data.total_price} /><span>ریال</span></span>

        </div>



      </DialogContent>
      <DialogActions>
        <Button onClick={() => handleClose()} variant="contained" color="primary" size="large">
          بستن
        </Button>
        {/* <Button onClick={() => handleClose()} color="primary">
          لغو
        </Button> */}
      </DialogActions>
    </Dialog>

  )
}

export default ServiceDetailsModal;