/*eslint-env jquery*/
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { toastr } from 'react-redux-toastr'
import MySweetAlert from '../../Shared/MySweetAlert'
import { messagesService } from '../../../_services/messages.service'
import { FaPlus } from 'react-icons/fa'
import { FiEdit } from 'react-icons/fi'
import { FiTrash2 } from 'react-icons/fi'
import MessageModal from './MessageModal'
import matchSorter from 'match-sorter'
import ReactTable from "react-table"
import messageTypes from '../../../_constants/messageTypes'




const requestData = (pageSize, page, sorted, filtered, dto) => {
  // console.log(dto)
  return new Promise(async (resolve, reject) => {

    // var dto = {};
    dto["pageSize"] = undefined;
    dto["page_size"] = pageSize;
    dto["page"] = page + 1;
    console.log(JSON.stringify(dto));

    var result = [];
    await messagesService.getAll(JSON.stringify(dto))
      .then((res) => {
        // console.log(res)
        result = res.data

      })
      .catch((err) => {
        setTimeout(() => requestData(pageSize, page, sorted, filtered, dto), 5000);
        // console.log(err);

      })


    setTimeout(() => resolve(result), 100);
  });
};

export class Messages extends Component {

  constructor(props, context) {
    super(props, context);

    var pageSize = parseInt(localStorage.getItem('pageSizeForProjects'), 0)
    this.state = {
      initialItems: [],
      messages: [],
      loading: true,
      confirmDeleteShow: false,
      requiredItem: 0,
      deleteItemIndex: 0,
      model: {},
      modal: false,
      pageSize: pageSize ? pageSize : 10,

      dto: { page: 0, pageSize: pageSize },
    }

    // this.getData()
  }

  fetchData = (state, instance) => {

    // Whenever the table model changes, or the user sorts or changes pages, this method gets called and passed the current table model.
    // You can set the `loading` prop of the table to true to use the built-in one or show you're own loading bar if you want.
    this.setState({ loading: true });
    // Request the data however you want.  Here, we'll use our mocked service we created earlier
    requestData(state.pageSize, state.page, state.sorted, state.filtered, this.state.dto).then(
      res => {
        console.log(Math.ceil(res.total_records / state.pageSize), res.data);

        if (res.data && res.data.length > 0) {
          // var pageSize = res.data.result.length > state.pageSize ? state.pageSize : res.data.result.length;
          // Now just get the rows of data to your React Table (and update anything else like total pages or loading)
          this.setState({
            data: res.data || [],
            pages: Math.ceil(res.total_records / state.pageSize),
            loading: false,
            modal: false,
            searchModal: false,
            pageSize: state.pageSize,
          });
        }
      }
    ).catch((err) => {
      console.log(err);

      this.setState({ loading: false, searchModal: false });
    })
  }

  handleSweetAlertHide = () => {
    this.setState({ confirmDeleteShow: false });
  }

  // getData() {
  //   messagesService.getAll()
  //     .then((res) => {
  //       console.log(res.data)
  //       this.setState({ initialItems: res.data, messages: res.data, loading: false });
  //     })
  // };

  handleError(error) {
    this.setState({ loading: false });
    // // toastr.warning('مشکلی پیش آمد', error);
  }

  handleDelete = (e) => {
    let _model = this.state.data[e];

    this.setState({ confirmDeleteShow: true, model: _model });
  }

  handleConfirmDelete = () => {
    this.setState({ loading: true, confirmDeleteShow: false });
    console.log(this.state.model);
    messagesService.delete(this.state.model.id)
      .then((res) => {
        toastr.success('حذف پیام ', 'پیام با موفقیت حذف شد');
        this.table.fireFetchData();
      })
      .catch((error) => {
        this.handleError(error)
      });
  }

  _handleSearch = (e) => {
    let searcjQery = e.target.value.toLowerCase(),
      displayedItems = this.state.initialItems.filter((el) => {
        let searchValue = el.name.toLowerCase();
        return searchValue.indexOf(searcjQery) !== -1;
      })
    this.setState({
      messages: displayedItems
    })
  }

  _handleCancelSearch = (e) => {
    document.getElementById("searchInput").value = '';
    this.setState({
      messages: this.state.initialItems
    })
  }

  handleModalShow = (i) => {
console.log(i);

    if (i !== undefined) {
      let _model = this.state.data[i];
      this.setState({ modal: true, model: _model });
    } else {
      let _model = {
        title: '',
        body: '',
        message_type: ''
      }
      this.setState({ modal: true, model: _model });
    }

  }

  handleModalClose = () => {

    this.setState({ modal: false });

  }

  handleModalCloseAndReload = () => {

    this.setState({ modal: false });

    this.table.fireFetchData();

  }

  render() {
    const { loading, data, pages, pageSize, dto, modal, model } = this.state;
    return (
      <div>

        <div className="row">
          <div className="col-lg-12">
            <a onClick={() => this.handleModalShow(undefined)} className="btn btn-primary btn-sm" style={{ float: 'right' }}>
              <FaPlus size="16px" style={{ verticalAlign: 'middle' }} />&nbsp;ارسال پیام جدید</a>
          </div>
        </div>

        <div className="row table-container" style={{ marginTop: 0 }}>
          <ReactTable


            manual
            data={data}
            pages={pages}
            loading={loading}
            onFetchData={this.fetchData}
            ref={(instance) => { this.table = instance; }}
            sorted={this.state.sorted}
            onSortedChange={(newSort, column) => {
              this.setState({ sorted: newSort });
            }}
            onPageSizeChange={pageSize => {
              // const { dispatch } = this.props;
              // dispatch( setPageSize(pageSize) )
              localStorage.setItem('pageSizeForProjects', pageSize);
              // this.setState({ page: 0 });
            }}
            page={this.state.page}
            onPageChange={page => this.setState({ page })}

            defaultPageSize={pageSize}
            previousText='صفحه قبل'
            nextText='صفحه بعد'
            loadingText='در حال بارگذاری ...'
            noDataText='نتیجه ای یافت نشد'
            pageText='صفحه'
            ofText='از'
            rowsText='تعداد نمایش'
            getTableProps={() => ({ style: { display: 'block' } })}
            getTrProps={(state, rowInfo, column, instance) => {
              const newSelected = Object.assign({}, this.state.selected);
              const selected = rowInfo ? newSelected[rowInfo.original.id] : false;
              return {
                style: {
                  backgroundColor: selected && "#5079F7",
                  color: selected && 'white',
                }
              }
            }}
            columns={[
              {
                Header: "ردیف",
                id: "row",
                maxWidth: 50,
                filterable: false,
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                className: "numberFa",
                Cell: (row) => {
                  return row.index + 1;
                }
              },
              {
                Header: "عنوان پیام",
                accessor: "body",
                id: "body",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["body"] }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              {
                Header: "نوع ارسال",
                accessor: "message_type",
                id: "message_type",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["message_type"] }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,
                Cell: (row) => (
                  <span className={"message-status-" + row.original.message_type}>
                    {row.original.message_type ? messageTypes.filter(el => el.value === row.original.message_type)[0].label: ''}
                  </span>
                )
              },
              {
                Header: "تاریخ ارسال",
                accessor: "created_at",
                id: "created_at",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["created_at"] }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,
                Cell: (row) => (
                  row.original.created_at
                )
              },
              {
                Header: "ویرایش",
                accessor: 'id',
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                sortable: false,
                Filter: ({ filter, onChange }) =>
                  <Link to="/admin/messages/new" className="btn btn-primary react-table-add-btn">
                    <span>
                      <FaPlus size="16px" />
                      <span>افزودن پیام جدید</span>
                    </span>
                  </Link>,
                Cell: row => (
                  <div>
                    <a onClick={() => this.handleModalShow(row.index)} className="btn btn-sm btn-simple btn-success bmd-ripple bmd-flat-btn" rel="tooltip" data-original-title="ویرایش"><FiEdit size="24px" /></a>
                    <a onClick={() => this.handleDelete(row.index)} className="btn btn-sm btn-simple btn-danger bmd-ripple bmd-flat-btn delete-btn" data-original-title="حذف" rel="tooltip"><FiTrash2 size="24px" /></a>
                  </div>
                )
              }
            ]}
            className="-striped -highlight"
          />

        </div>

        <MySweetAlert
          show={this.state.confirmDeleteShow}
          onCancel={this.handleSweetAlertHide}
          onConfirm={this.handleConfirmDelete}
          title={"آیا از حذف پیام این مطمئن هستید؟"}
        >
        </MySweetAlert>

        <MessageModal data={model} open={modal} handleModalClose={() => this.handleModalClose} handleModalCloseAndReload={() => this.handleModalCloseAndReload()} />

        {/* {loading ? <Loading /> : ''} */}
      </div>
    );
  }
}

