import React, {  useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import { reasonsService } from '../../../_services/reasons.service'
import CreateServiceModal from './CreateServiceModal';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { ServicesList } from './List'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import DoneAll from '@material-ui/icons/DoneAll';
import LibraryBooks from '@material-ui/icons/LibraryBooks';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`nav-tabpanel-${index}`}
      aria-labelledby={`nav-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

// TabPanel.propTypes = {
//   children: PropTypes.node,
//   index: PropTypes.any.isRequired,
//   value: PropTypes.any.isRequired,
// };

const useStyles = makeStyles(theme => ({

  topbar: {
    background: "#67b65d",
    color: "#fff",
  },
  menuButton: {
    color: "#fff"
  }
}))




export const Services = (props) => {
  const [tabValue, setTabValue] = React.useState(0);
  const [showDialog, setShowDialog] = useState(false)
  const [reasons, setReasons] = useState([])
  const [contractId, setContractId] = useState(props.match.params.id)
  const [reloadServicesList, setReloadServicesList] = useState(false)
  // const items = useRef([])
  // const [loading, setLoading] = useState(false)
  // const [initLoading, setInitLoading] = useState(true)
  // const [endReached, setEndReached] = useState(false)

  const classes = useStyles()

  useEffect(() => {

    if (props.match.params.id) {
      setContractId(props.match.params.id)
      getReasons()
    }


  }, [props.match.params.id])

  const handleTabChange = (event, newValue) => {
    // setPage(0)
    // setData([])
    // setLoading(true)
    // setEndReached(false)
    setTabValue(newValue);
    // console.log(newValue);

  };

  const getReasons = () => {
    reasonsService.getAll()
      .then((res) => {
        // console.log(res.data)
        setReasons(res.data)
      })
  }

  const handleModalShow = () => {
    setShowDialog(true)
  }




  return (
    <React.Fragment>
      <AppBar position="static" color="default" className={classes.appbar}>
        <Toolbar className={classes.topbar}>
          <IconButton className={classes.menuButton} color="default" aria-label="Menu">
            <Link to="/customer"><NavigateBeforeIcon style={{ color: '#fff' }} /></Link>
          </IconButton>
          <Typography
            variant="inherit"
            color="inherit"
            className={classes.logo}
          // onClick={() => history.push("home")}
          >
            {/* <img className={styles.imgLogo} src={logo} alt="Logo" />{" "} */}
            {/* <span className={styles.txtLogo}>{appconf('NAME')}</span> */}
          </Typography>
          <Typography
            variant="inherit"
            color="inherit"
            className="appbar-title">
            سرویس ها
          </Typography>
        </Toolbar>
      </AppBar>
      <div className="orders projects">

        <Tabs
          value={tabValue}
          onChange={handleTabChange}
          variant="fullWidth"
          indicatorColor="primary"
          textColor="primary"
          aria-label="icon label tabs"
          style={{ marginBottom: 5 }}
        >
          <Tab icon={<LibraryBooks />} value={0} label="جدید" />
          <Tab icon={<DoneAll />} value={1} label="انجام شده" />
        </Tabs>

        <div className={tabValue === 0 ? 'show' : 'hidden'} style={{ height: 'calc(100vh - 210px)' }}>
          {tabValue === 0 &&
            <ServicesList status={0} contract_id={contractId} reloadList={reloadServicesList} />
          }
        </div>
        <div className={tabValue === 1 ? 'show' : 'hidden'} style={{ height: 'calc(100vh - 210px)' }}>
          {tabValue === 1 &&
            <ServicesList status={1} contract_id={contractId} />
          }
        </div>
        <Fab aria-label="add" className="fab-add-btn" size="medium" color="primary" onClick={() => handleModalShow()}>
          <AddIcon />
        </Fab>

      </div>
      {/* <Loading loading={initLoading} /> */}
      <CreateServiceModal data={reasons} open={showDialog}
        onCloseAndReload={() => {
          setTabValue(0)
          setReloadServicesList(true)
          window.location.reload()
          setShowDialog(false)
        }}
        onClose={() => {
          setShowDialog(false)
        }}
        contractId={contractId} />

    </React.Fragment >
  );

}
