import React from 'react'
import { Route, Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { FaUserCog } from 'react-icons/fa'
import { MdDashboard, MdSettings } from 'react-icons/md'

// import { userActions } from '../../_actions'

class NavMenu extends React.Component {
  // componentDidMount() {
  //     this.props.dispatch(userActions.getAll())
  // }
  render() {
    const { user, handleSidebarHide } = this.props;
    return (
      <ul className="nav">


        <Route path="/admin" exact>
          {({ match }) => <li onClick={handleSidebarHide} onClick={handleSidebarHide} className={match ? 'nav-item root-li active' : 'nav-item root-li'}><Link to="/admin">
            <MdDashboard className="mdi" size="24px" />
            <span>
              داشبورد
                  </span>
          </Link></li>}
        </Route>

        {user && user.role !== undefined && user.role === 0 &&
          <>
            {/* <Route path="/admin/categories" exact>
              {({ match }) => <li onClick={handleSidebarHide} onClick={handleSidebarHide} className={match ? 'nav-item root-li active' : 'nav-item root-li'}><Link to="/admin/categories">
                <FaLayerGroup className="mdi" size="24px" />
                <span>
                  دسته بندی ها
                  </span>
              </Link></li>}
            </Route>
            <Route path="/admin/products" exact>
              {({ match }) => <li onClick={handleSidebarHide} onClick={handleSidebarHide} className={match ? 'nav-item root-li active' : 'nav-item root-li'}><Link to="/admin/products">
                <FaProductHunt className="mdi" size="24px" />
                <span>
                  محصولات
                  </span>
              </Link></li>}
            </Route> */}
            <Route path="/admin/contracts" exact>
              {({ match }) => <li onClick={handleSidebarHide} onClick={handleSidebarHide} className={match ? 'nav-item root-li active' : 'nav-item root-li'}><Link to="/admin/contracts">
                <FaUserCog className="mdi" size="24px" />
                <span>
                  قرارداد ها
                  </span>
              </Link></li>}
            </Route>
            <Route path="/admin/users" exact>
              {({ match }) => <li onClick={handleSidebarHide} onClick={handleSidebarHide} className={match ? 'nav-item root-li active' : 'nav-item root-li'}><Link to="/admin/users">
                <FaUserCog className="mdi" size="24px" />
                <span>
                  کاربران
                  </span>
              </Link></li>}
            </Route>
            <Route path="/admin/messages" exact>
              {({ match }) => <li onClick={handleSidebarHide} onClick={handleSidebarHide} className={match ? 'nav-item root-li active' : 'nav-item root-li'}><Link to="/admin/messages">
                <FaUserCog className="mdi" size="24px" />
                <span>
                  پیام ها
                  </span>
              </Link></li>}
            </Route>
          </>
        }

        {user && user.role !== undefined && user.role === 0 &&
          <Route path="/admin/Settings/:path(standards|insurances|reasons)">
            {({ match }) =>
              <li className={match ? 'nav-item root-li active' : 'nav-item root-li'}>
                <a className={match ? 'nav-link' : 'nav-link collapsed'} data-toggle="collapse" aria-expanded={match ? 'true' : 'false'}
                  href="#Settings">
                  <MdSettings className="mdi" size="24px" />
                  <span>
                    اطلاعات پایه
                  </span>
                  <i className="mdi mdi-chevron-down "></i>

                </a>
                <div className={match ? 'collapse in' : 'collapse'} aria-expanded={match ? 'true' : 'false'} id="Settings">
                  <ul className="nav">
                    <Route path="/admin/Settings/standards" exact>
                      {({ match }) => <li onClick={handleSidebarHide} className={match ? 'nav-item active' : 'nav-item'}><Link to="/admin/Settings/standards">شرکت های استاندارد</Link></li>}
                    </Route>
                    <Route path="/admin/Settings/insurances" exact>
                      {({ match }) => <li onClick={handleSidebarHide} className={match ? 'nav-item active' : 'nav-item'}><Link to="/admin/Settings/insurances">شرکت های بیمه</Link></li>}
                    </Route>
                    <Route path="/admin/Settings/reasons" exact>
                      {({ match }) => <li onClick={handleSidebarHide} className={match ? 'nav-item active' : 'nav-item'}><Link to="/admin/Settings/reasons">لیست دلایل خرابی</Link></li>}
                    </Route>
                    <Route path="/admin/Settings/denyReasons" exact>
                      {({ match }) => <li onClick={handleSidebarHide} className={match ? 'nav-item active' : 'nav-item'}><Link to="/admin/Settings/denyReasons">لیست دلایل انصراف</Link></li>}
                    </Route>
                  </ul>
                </div>
              </li>
            }
          </Route>
        }


      </ul>
    );
  }
}


function mapStateToProps(state) {
  const { authentication } = state;
  const { user } = authentication;
  return {
    user
  };
}

const connectedNavMenu = connect(mapStateToProps)(NavMenu);
export { connectedNavMenu as NavMenu };