import axios from 'axios';
import { handleSuccess, handleError } from './axiosHandler';
import { authHeader, authHeaderJson } from '../_helpers/auth-header';

import {config} from '../config';
const base_url = config.apiUrl;

const model = 'dashboard/';

export const dashboardService = {
  getAll,
  getContracts,
  denied,
  assign_user,
  setFirebaseToken,
};

function getAll() {
  const url = base_url + model;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader()});
}

function getContracts() {
  const url = base_url + model + 'contracts';
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader()});
}

function denied() {
  const url = base_url + model + 'denied';
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader()});
}

function assign_user(dto) {
  const url = base_url + model + 'assign_user';
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson()});
}

function setFirebaseToken(dto) {
  const url = base_url + 'user/' + 'update_firebase_token';
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson()});
}