import React, { useState, useEffect } from 'react'
import ReactTable from "react-table"
import Loading from '../../../Shared/Loading'

const Tab3 = (props) => {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);

  useEffect(() => {

    setData(props.data.today_services)
    setLoading(props.loading)

  }, [props.data, props.loading])

  return (
    <React.Fragment>

      <div className="row table-container" style={{ marginTop: 0 }}>
        <ReactTable
          data={data}
          filterable

          previousText='صفحه قبل'
          nextText='صفحه بعد'
          loadingText='در حال بارگذاری ...'
          noDataText='نتیجه ای یافت نشد'
          pageText='صفحه'
          ofText='از'
          rowsText='تعداد نمایش'
          getTableProps={() => ({ style: { display: 'block' } })} // direction: rtl fix

          defaultFilterMethod={(filter, row) =>
            String(row[filter.id]) === filter.value}
          columns={[
            {
              Header: "ردیف",
              id: "row",
              maxWidth: 50,
              filterable: false,
              style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
              className: "numberFa",
              Cell: (row) => {
                return row.index + 1;
              }
            },
            {
              Header: "شماره قرارداد",
              accessor: "contract_id",
              id: "contract_id",
              style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
              filterable: false,
              Cell: row => (
                row.original.contract.contract_number
              )
            },
            {
              Header: "طرف قرارداد",
              accessor: "customer_fullName",
              id: "customer_fullName",
              style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
              filterable: false,
              Cell: row => (
                row.original.contract.customer && row.original.contract.customer.full_name
              )
            },
            {
              Header: "نام سرویس کار",
              accessor: "user_fullName",
              id: "user_fullName",
              style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
              filterable: false,
              Cell: row => (
                row.original.contract.user && row.original.contract.user.full_name && row.original.contract.user.full_name
              )
            }
          ]}
          defaultPageSize={10}
          className="-striped -highlight"
        />

      </div>

      {loading ? <Loading /> : ''}

    </React.Fragment>
  )
}
export default Tab3;