import React from "react";
import { hot } from "react-hot-loader";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import { FaPowerOff, FaComments, FaLayerGroup, FaHome } from 'react-icons/fa'
import { RiLockPasswordLine } from 'react-icons/ri'
import {history} from '../../../_helpers'

const styles = theme => ({
  list: {
    width: 250
  }
});

const SwipeDrawer = props => {

  const exitApp = () => {

    if (navigator.app) {
      navigator.app.exitApp();
    } else if (navigator.device) {
      navigator.device.exitApp();
    }

  }

  return (
    <SwipeableDrawer
      anchor="right"
      open={props.openDrawer}
      onClose={() => props.toggleDrawer()}
      onOpen={() => props.toggleDrawer()}
    >
      <div
        tabIndex={0}
        role="button"
        onClick={() => props.toggleDrawer()}
        onKeyDown={() => props.toggleDrawer()}
      >
        <List component="nav">
          <ListItem button onClick={() => history.push("/customer")}>
            <ListItemIcon>
              <FaHome size="24px" />
            </ListItemIcon>
            <ListItemText primary="دشبورد" />
          </ListItem>
        </List>
        <Divider />
        <List component="nav">
          <ListItem button onClick={() => history.push("/customer/contracts")} >
            <ListItemIcon>
              <FaLayerGroup size="24px" />
            </ListItemIcon>
            <ListItemText primary="قرارداد ها" />
          </ListItem>
        </List>
        <Divider />
        {/* <List component="nav">
          <ListItem button onClick={() => history.push("/customer/services")} >
            <ListItemIcon>
              <FaTools size="24px" />
            </ListItemIcon>
            <ListItemText primary="سرویس ها" />
          </ListItem>
        </List>
        <Divider /> */}
        <List component="nav">
          <ListItem button onClick={() => history.push("/customer/messages")} >
            <ListItemIcon>
              <FaComments size="24px" />
            </ListItemIcon>
            <ListItemText primary="پیام ها" />
          </ListItem>
        </List>
        <Divider />
        <List component="nav">
          <ListItem button onClick={() => history.push("/login/resetPassword")} >
            <ListItemIcon>
              <RiLockPasswordLine size="24px" />
            </ListItemIcon>
            <ListItemText primary="تغییر کلمه عبور" />
          </ListItem>
        </List>
        <Divider />
        <List component="nav">
          <ListItem button onClick={() => {localStorage.clear();history.push("/login")}}>
            <ListItemIcon>
              <FaPowerOff size="24px" />
            </ListItemIcon>
            <ListItemText primary="خروج از حساب" />
          </ListItem>
        </List>
        <Divider />
        {/* <List component="nav">
          <ListItem button onClick={() => exitApp()}>
            <ListItemIcon>
              <AiOutlineClose size="24px" />
            </ListItemIcon>
            <ListItemText primary="بستن برنامه" />
          </ListItem>
        </List> */}
        <Divider />
      </div>
    </SwipeableDrawer>
  )
}

export default hot(module)(SwipeDrawer);