import React, { Component } from 'react'
import { contractsService } from '../../../_services/contracts.service'
import { AiOutlineLoading } from "react-icons/ai"
import { Virtuoso } from 'react-virtuoso'
import { makeStyles } from '@material-ui/core/styles'
import List from '@material-ui/core/List'
import ProjectItem from './Item'
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';

const Transition = React.forwardRef(function Transition(props, ref) {
	return <Slide direction="down" ref={ref} {...props} />;
});


// import faker from 'faker'
// import { groupBy } from 'lodash'

// import LazyLoad from 'react-lazyload'


const useStyles = makeStyles(theme => ({

	list: {
		margin: 0,
		padding: 0,
	},
	listItem: {
		margin: 0,
		padding: 10,
	},
	orderDetails: {
		direction: 'rtl',
		textAlign: 'right',
		marginRight: 10,

	},
	inline: {
		display: 'inline',
	},

	root: {
		flexGrow: 1
	},
	logo: {
		flexGrow: 1,
		textAlign: "center",
		cursor: "pointer",
		fontSize: 12
	},
	imgLogo: {
		display: "inline-block",
		height: 32,
		width: 32
	},
	txtLogo: {
		display: "inline-block",
		fontSize: 16,
		lineHeight: "32px",
		verticalAlign: "bottom"
	},
	menuButton: {
		// marginRight: -theme.spacing.unit
	},
	appconfbar: {
		boxShadow:
			"0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12)"
	},
	bottomNav: {
		position: "fixed",
		bottom: "0",
		width: "100%",
		boxShadow:
			"0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12)"
	},
	bottomNavAct: {
		minWidth: "72px",
		maxWidth: "120px"
	},
	bigAvatar: {
		width: 60,
		height: 60,
	},
}))



class ProjectList extends Component {

	constructor(props, context) {
		super(props, context);

		this.state = {
			initLoading: true,
			loading: false,
			data: [],
			page: 1,
			page_size: 10,
			total: 0,
			endReached: false,
			dialogData: { title: '', text: '' }
		}

	}

	componentDidMount() {
		this.loadMore()
	}

	loadMore = () => {

		const { data, page, page_size, projectStatus, endReached } = this.state;
		// console.log(data, page, page_size, projectStatus, endReached);

		// setTimeout(() => {
		if (!endReached) {
			// setLoading(true)
			this.setState({ loading: true });

			const dto = {
				page: page,
				page_size: page_size,
			};


			// setPage(page + 1)

			contractsService.getAllByUser(dto)
				.then((res) => {
					console.log(res.data)

					let newData = data.concat(res.data.data);

					let total = res.data.totalRecords;
					let endReached = (newData.length === total) ? true : false;
					this.setState({ data: newData, total: newData.length, endReached: endReached, page: page + 1, loading: false, initLoading: false })

				})
				.catch((error) => {
					console.log(error);
					this.setState({ loading: false, initLoading: false });
				});

		}

	}

	handleDialogOpen = (id, index) => {
		console.log(id, index);

		let newData = this.state.data;
		let dialogData = newData[index];
		if (dialogData) {
			this.setState({ dialog: true, dialogData: dialogData })


			contractsService.getById(id)
				.then((res) => {
					// console.log(res.data)


					let user = JSON.parse(localStorage.getItem('user'));
					user["unread_messages"] = user.unread_messages - 1;
					if (user.unread_messages > 0) {
						localStorage.removeItem('user');
						localStorage.setItem('user', JSON.stringify(user));
					}


					newData[index]["messageStatus"] = 1;
					this.setState({ data: newData })
				})
				.catch((error) => {
					console.log(error);
					this.setState({ loading: false });
					// toastr.info("مشکلی در دریافت اطلاعات بوجود آمد. لطفا دوباره سعی کنید.")
				});
		}

	}

	handleDialogClose = () => {
		this.setState({ dialog: false })
	}

	render() {
		const { data, total, loading, endReached, initLoading, dialog, dialogData } = this.state;
		return (
			<>
				{initLoading ?

					<div className="messages-init-loading">
						<AiOutlineLoading className="mdi-spin" size="32px" />
					</div>

					:

					data.length > 0 ?

						<Virtuoso
							style={{ width: '100%', height: '100%' }}
							totalCount={total}
							item={(index) => {
								return (
									<ProjectItem data={data[index]} key={index}
									// onClickItem={val => this.handleDialogOpen(val, index)} 
									/>
								)
							}}
							endReached={() => this.loadMore()}
							ListContainer={({ listRef, className, style, children }) => {
								return (
									<List
										ref={listRef}
										style={style}
									>
										{children}
									</List>
								)
							}}
							// ItemContainer={({ children, ...props }) => {
							//   return (
							//     <ListItem button {...props} style={{ margin: 0 }}>
							//       {children}
							//     </ListItem>
							//   )
							// }}
							footer={() => {
								return !endReached && (
									<div className="list-loading-more">
										{/* <button disabled={loading} onClick={loadMore}> */}
										{loading ? 'در حال بارگذاری، لطفا صبر کنید ...' : ''}
										{/* </button> */}
									</div>
								)
							}}
						/>
						:

						<div className="messages-init-loading">موردی برای نمایش یافت نشد</div>
				}

				<Dialog
					className="message-dialog"
					open={dialog}
					TransitionComponent={Transition}
					keepMounted
					onClose={this.handleDialogClose}
					aria-labelledby="alert-dialog-slide-title"
					aria-describedby="alert-dialog-slide-description"
				>
					<DialogTitle id="alert-dialog-slide-title">{dialogData.title}</DialogTitle>
					<DialogContent>
						<DialogContentText id="alert-dialog-slide-description">
							{dialogData.text}
						</DialogContentText>
					</DialogContent>
					<DialogActions>

						<Button variant="outlined" size="small" color="primary" onClick={this.handleDialogClose} color="primary">
							بستن
          </Button>
					</DialogActions>
				</Dialog>
			</>

		);
	}
}

export default ProjectList;
