import React, { PureComponent } from 'react'
import { standardsService } from '../../_services/standards.service'
import { Dropdown } from 'semantic-ui-react'


const _data = [
  { value: 0, text: '1:1' },
  { value: 1, text: '1:2' },
  { value: 2, text: 'جک مستقیم' },
  { value: 3, text: 'جک از بغل' }
]

class SuspensionTypeSelect extends PureComponent {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      defaultValue: this.props.defaultValue,
      type: this.props.type,
      data: this.props.type === 0 ? _data.filter(el => el.value > 1) : _data.filter(el => el.value < 2),
    };
  }

  async componentWillReceiveProps(nextProps) {

    if (nextProps.defaultValue !== this.state.defaultValue) {
      this.setState({ defaultValue: nextProps.defaultValue });
    }

    if (nextProps.type !== this.state.type) {
      this.setState({ type: nextProps.type, data: nextProps.type === 0 ? _data.filter(el => el.value > 1) : _data.filter(el => el.value < 2) });
    }

  }

  handleChange = (e) => {

    let selected = e;
    this.setState({ defaultValue: selected })
    this.props.onChange(selected)

  }

  render() {

    const { data, loading, defaultValue, disabled } = this.state;
    const { clearable, className } = this.props;
    // console.log(data);

    return (
      <Dropdown
        placeholder=''
        fluid
        search
        selection
        basic
        onChange={(e, data) => this.handleChange(data.value)}
        defaultValue={defaultValue}

        className={className}
        options={data}
        wrapSelection={false}
        selectOnBlur={false}
        searchInput={{ autoFocus: false }}
        loading={loading}
        disabled={disabled}
        clearable={clearable}
      />
    );
  }
}
export default SuspensionTypeSelect;
