/*eslint-env jquery*/
import React, { Component } from 'react'
import { toastr } from 'react-redux-toastr'
import Loading from '../../Shared/Loading'
import { Tabs, Tab } from 'react-bootstrap'
import { history } from '../../../_helpers'
import { contractsService } from '../../../_services/contracts.service'

import { Formik } from 'formik'
import * as Yup from 'yup'

import Step1 from './Step1'
import Step2 from './Step2'

import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai";

const init_data = {
  'building_number': '',
  'contract_number': '',

  'elevator_count': '',
  'stops': '',
  'address': '',

  'start_date': '',
  'finish_date': '',
  'total_price': '',
  'payment_price': '',

  'service_day': '',
  'user_id': '',
  "customer_id": '',

  'description': '',

  'elevators': [{
    'serial_number': '',
    'elevator_type': null,
    'usage': null,
    'capacity': '',
    'floors': '',

    'door_type': null,
    'stops': '',
    'swing': '',
    'automatic': '',
    'door_name': '',

    'engine_room': null,
    'suspension_type': null,
    'engine': '',
    'engine_type': null,
    'power': '',

    'panel_type': null,
    'panel_name': '',
    'drive': '',
    'feedback': null,
    'emergency_system': null,

    'car_communication': null,
    'speed': '',

    'insurance_id': null,
    'insurance_date': '',
    'insurance_finish_date': '',

    'standard_id': null,
    'standard_finish_date': '',
    'standard_type': null,
  }]
}

class Form extends Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      loading: true,
      key: 'info',
      id: this.props.match.params.id,
      data: undefined,
    }
  }




  componentDidMount() {
    this.getData()
  }

  getData = async () => {

    const { id } = this.state;

    if (id) {

      this.setState({ loading: true });

      await contractsService.getById(id)
        .then((res) => {
          console.log(res.data);

          this.setState({ data: res.data, loading: false })

        })
        .catch((error) => {

          console.log(error);

          this.setState({ data: init_data, loading: false });

        });

    } else {

      this.setState({ data: init_data, loading: false });

    }


  };

  handleSubmit = (values, setSubmitting) => {
    // this.setState({ loading: true });
    console.log(JSON.stringify(values));

    if (values.id) {

      contractsService.edit(values.id, JSON.stringify(values))
        .then((res) => {

          toastr.success('قرارداد با موفقیت ویرایش شد');
          // history.push("/admin/contracts")
          setSubmitting(false);

        })
        .catch((error) => {
          setSubmitting(false);
        });

    } else {

      contractsService.add(JSON.stringify(values))
        .then((res) => {
          console.log(res);

          this.setState({ loading: false, data: res.data, key: "images" });
          toastr.success('قرارداد جدید  با موفقیت ثبت شد');
          history.push("/admin/contracts")
          setSubmitting(false);


        })
        .catch((error) => {
          console.log(error)
          setSubmitting(false);
        });

    }
  }

  handleError(error) {
    this.setState({ loading: false });
    // toastr.warning('مشکلی پیش آمد', error);
  }

  validateForm = (errors) => {
    if (Object.keys(errors).length > 0) {
      // console.log(errors)
      let obj = errors
      let { createdDate, workStartDate, firstName, lastName, codeMeli, idnumber, birthDate, cellNumber, address, travelBrandId, gender, degree, childrenCount,
        driverDocuments,
        user
      } = obj;
      if (createdDate || workStartDate || firstName || lastName || codeMeli || idnumber || birthDate || cellNumber || address || travelBrandId || gender || degree || childrenCount) {
        this.setState({ key: "info" })
      } else if (driverDocuments) {
        this.setState({ key: "images" })
      }
    }
  }

  render() {
    const { data, loading } = this.state;


    const elevator = Yup.object().shape({
      serial_number: Yup.string().trim().nullable().required('شناسه ملی'),
      elevator_type: Yup.string().trim().nullable().required('نوع آسانسور'),
      usage: Yup.string().trim().nullable().required('کاربری'),
      capacity: Yup.string().trim().nullable().required('ظرفیت'),
      floors: Yup.string().trim().nullable().required('تعداد طبقات'),

      door_type: Yup.string().trim().nullable().required('نوع درب کابین'),
      stops: Yup.string().trim().nullable().required('تعداد توقف'),
      swing: Yup.string().trim().nullable().required('تعداد درب لولایی'),
      automatic: Yup.string().trim().nullable().required('تعداد درب اتوماتیک'),
      door_name: Yup.string().trim().nullable().required('نام درب اتوماتیک'),

      engine_room: Yup.string().trim().nullable().required('موتور خانه'),
      suspension_type: Yup.string().trim().nullable().required('سیستم تعلیق/موقعیت جک'),
      engine: Yup.string().trim().nullable().required('نوع موتور'),
      engine_type: Yup.string().trim().nullable().required('مدل موتور'),
      power: Yup.string().trim().nullable().required('توان موتور'),

      panel_type: Yup.string().trim().nullable().required('نوع تابلو فرمان'),
      panel_name: Yup.string().trim().nullable().required('نام تابلو'),
      drive: Yup.string().trim().nullable().required('نام درایو'),
      feedback: Yup.string().trim().nullable().required('feedback'),
      emergency_system: Yup.string().trim().nullable().required('سیستم نجات اضطراری'),

      car_communication: Yup.string().trim().nullable().required('سیستم ارتباط کابین'),
      speed: Yup.string().trim().nullable().required('سرعت آسانسور'),

      // insurance_id: Yup.string().trim().nullable().required(''),
      // insurance_date: Yup.string().trim().nullable().required(''),
      // insurance_finish_date: Yup.string().trim().nullable().required(''),

      // standard_id: Yup.string().trim().nullable().required(''),
      // standard_finish_date: Yup.string().trim().nullable().required(''),
      standard_type: Yup.string().trim().nullable().required('نوع استاندارد'),


    });

    const ValidationSchema = Yup.object().shape({

      // building_number: Yup.string().trim().nullable().required('کد ساختمان'),
      contract_number: Yup.string().trim().nullable().required('شماره قرارداد'),
      stops: Yup.string().trim().nullable().required('تعداد توقف'),
      // address: Yup.string().trim().nullable().required('آدرس محل نصب'),
      start_date: Yup.string().trim().nullable().required('تاریخ شروع قرارداد'),
      finish_date: Yup.string().trim().nullable().required('تاریخ اتمام قرارداد'),
      total_price: Yup.string().trim().nullable().required('مبلغ کل قرارداد'),
      // payment_type: Yup.string().trim().nullable().required('نوع پرداخت'),
      service_day: Yup.string().trim().nullable().required('روزهای سرویس'),
      customer_id: Yup.string().trim().nullable().required('انتخاب مشتری'),
      user_id: Yup.string().trim().nullable().required('انتخاب سرویس کار'),

      // elevators: Yup.array().of(elevator).min(1, 'تمامی اطلاعات').required('تمامی اطلاعات آسانسور'),
    });
    return (
      !loading ?
        <Formik
          enableReinitialize
          initialValues={data}
          onSubmit={(values, { setSubmitting }) => {
            setSubmitting(true);
            this.handleSubmit(values, setSubmitting);
          }}
          validationSchema={ValidationSchema}
        >
          {props => {
            const {
              values,
              touched,
              errors,
              setFieldValue,
              setFieldTouched,
              isSubmitting,
              handleChange,
              handleBlur,
              handleSubmit,
              validateForm,
            } = props;
            return (
              <div>
                <div className="row" style={{ position: "relative" }}>
                  <form onSubmit={handleSubmit}>
                    <button type="submit" className={"icon button submit-btn " + (isSubmitting ? "isSubmitting" : "")} disabled={isSubmitting}
                      onClick={() =>
                        validateForm().then(errors => {
                          console.log(errors);
                        })
                      } >
                      {isSubmitting ?
                        <AiOutlineLoading className="mdi-spin" size="22px" />
                        :
                        <AiOutlineSave size="22px" />

                      }
                      ذخیره
                  </button>
                    <Tabs transition={false}
                      id="controlled-tab-example"
                      activeKey={this.state.key}
                      onSelect={key => this.setState({ key })}
                    >

                      <Tab eventKey="info" title="اطلاعات قرارداد">

                        <Step1 {...props} />

                      </Tab>
                      <Tab eventKey="map" title="مختصات محل">

                        <Step2 {...props} tabChanged={this.state.key === "map" ? true : false} />

                      </Tab>

                    </Tabs>
                  </form>
                </div>
                {this.state.loading ? <Loading /> : ''}
              </div>
            );
          }}
        </Formik>
        :
        <Loading loading={loading} />

    );
  }
}

export default Form;
