import React from 'react'
import { Route, Link } from 'react-router-dom'
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import MessageList from './List'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`nav-tabpanel-${index}`}
      aria-labelledby={`nav-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

// TabPanel.propTypes = {
//   children: PropTypes.node,
//   index: PropTypes.any.isRequired,
//   value: PropTypes.any.isRequired,
// };

const useStyles = makeStyles(theme => ({

  topbar: {
    background: "#67b65d",
    color: "#fff",
  },
  menuButton: {
    color: "#fff"
  }
}))




const Messages = (props) => {

  const classes = useStyles()

  return (
    <React.Fragment>
      <AppBar position="static" color="default" className={classes.appbar}>
        <Toolbar className={classes.topbar}>
          <IconButton className={classes.menuButton} color="default" aria-label="Menu">
            <Route path="/customer" >
              {({ match }) => <Link to={match ? "/customer" : "/technician"}><NavigateBeforeIcon style={{ color: '#fff' }} /></Link>}
            </Route>

          </IconButton>
          <Typography
            variant="inherit"
            color="inherit"
            className={classes.logo}
          // onClick={() => history.push("home")}
          >
            {/* <img className={styles.imgLogo} src={logo} alt="Logo" />{" "} */}
            {/* <span className={styles.txtLogo}>{appconf('NAME')}</span> */}
          </Typography>
          <Typography
            variant="inherit"
            color="inherit"
            className="appbar-title">
            سوابق مالی
          </Typography>
        </Toolbar>
      </AppBar>
      <div className="messages orders">


        <MessageList />


      </div>
      {/* <Loading loading={initLoading} /> */}

    </React.Fragment >
  );

}

export default Messages;
