import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import ListItem from '@material-ui/core/ListItem'
import Divider from '@material-ui/core/Divider';
import ListItemText from '@material-ui/core/ListItemText';
import { GoCalendar } from 'react-icons/go'
import { FaHashtag } from 'react-icons/fa'
import { history } from '../../../_helpers'
import { FaPhone } from 'react-icons/fa'


const useStyles = makeStyles(theme => ({
  listItem: {
    margin: 0,
    padding: 10,
  },
}))

const ProjectItem = ({ data }) => {


  const classes = useStyles()

  return (
    <div onClick={() => history.push("/Technician/services/" + data.contract.id + "/serviceInfo/" + data.id)}>
      <div className="first-row">
        <span>
          <FaHashtag size="16px" />
          <span>{data.contract.contract_number}</span>
        </span>
        <span> </span>
        <span>
          <GoCalendar size="16px" />
          <span>{data.created_at}</span>
        </span>
      </div>
      <ListItem button className={classes.listItem} >
        <Divider />
        <ListItemText
          className="order-details"
          primary={data.customer.full_name}
          secondary={<span><FaPhone />{data.customer.cell}</span>}
        />
        {/* {data.open ? <ExpandLess /> : <ExpandMore />} */}
      </ListItem>
      <div className="last-row">
        <span>نوع سرویس: {data.request_type === 0 ? 'ماهانه' : 'خرابی'}</span>
        <span>
        </span>
      </div>
      {/* <Collapse in={data.open} timeout="auto" unmountOnExit>
      <List component="div" disablePadding>
        <ListItem button className={classes.nested}>
          test
        </ListItem>
      </List>
    </Collapse> */}
    </div>
  )
}

export default ProjectItem