/*eslint-env jquery*/
import React, { PureComponent } from 'react'
import { utils, Calendar as Calendar_ } from 'react-modern-calendar-datepicker';
// import {moment} from 'moment'
// // import moment from "moment-jalaali";
// import fa from "../../../node_modules/moment/locale/fa";
// moment.locale("fa", fa);
// moment.loadPersian(); 
// import moment from 'jalali-moment';
// moment().locale('fa').format('YYYY/M/D');
import * as moment from 'moment-jalaali'

class Calendar extends PureComponent {

  constructor(props) {
    super(props);
    var m = new moment();
    this.state = {
      // _moment: moment(),
      defaultValue: undefined,
      cleared: true,
      loading: true,
      // value: this.props.defaultValue ?
      //   this.formatDefaultValue(this.props.defaultValue) : ''
    };


    // console.log(this.props.defaultValue)
  }

  handleChange = (val) => {

    let formattedDate = moment(val.year + '/' + val.month + '/' + val.day, "jYYYY/jMM/jDD").format('YYYY-MM-DD');

    console.log(formattedDate);

    this.props.onChange(formattedDate)

  }


  componentWillReceiveProps(nextProps) {
    
    if (nextProps.defaultValue) {

      let formattedDate = moment(nextProps.defaultValue, 'YYYY-MM-DD');
      
      let date = {
        year: parseInt(formattedDate.format('jYYYY'), 0),
        month: parseInt(formattedDate.format('jM'), 0),
        day: parseInt(formattedDate.format('jD'), 0),
      };

      this.setState({ defaultValue: date })
    }

  }



  render() {
    // const { _moment, cleared, inputValue } = this.state;

    // const { name, onChange, handleBlur, disabled, minDate, maxDate, tabIndex } = this.props;

    // const newMoment = moment();


    // var value = !cleared && _moment ? _moment.format("jYYYY/jMM/jDD") : "";

    return (
      <Calendar_
        locale="fa"
        minimumDate={utils('fa').getToday()}
        colorPrimary="#d12639"
        colorPrimaryLight="#f24154"
        inputPlaceholder=" "
        shouldHighlightWeekends

        inputClassName="text-input datepicker-input"
        onChange={val => { this.handleChange(val) }}
        value={this.state.defaultValue}
      />
    );
  }
}
export default Calendar;
