import React from 'react'
import {
  // BrowserView,
  MobileView,
  // isBrowser,
  // isMobile
} from "react-device-detect";
import BottomNav from "./BottomNav";
import SwipeDrawer from "./SwipeDrawer";
import { ShowPushNotification } from '../../../push-notification'


class TechnicianLayout extends React.Component {
  state = {
    bottomNav: "",
    openDrawer: false
  };
  changeNav = (value) => {
    if (value === "menu") {
      this.toggleDrawer();
    } else {
      // this.props.history.push(value);
      this.setState({ bottomNav: value });
    }
  };

  toggleDrawer = () => {
    this.setState({ openDrawer: !this.state.openDrawer });
  };

  render() {
    const { openDrawer, bottomNav } = this.state;


    return (
      <MobileView>
        <ShowPushNotification />
          <div style={{ flexGrow: 1 }}>
        
            {this.props.children}
            <BottomNav
            changeNav={this.changeNav} 
            value={bottomNav} 
            />
      
            <SwipeDrawer
            // login={login}
            // history={history}
            openDrawer={openDrawer}
            toggleDrawer={this.toggleDrawer}
          // Logout={this.Logout}
          />
          </div>
      </MobileView>
    );
  }
}


export default TechnicianLayout;