import React, { useState, useEffect } from 'react'
import Modal from 'react-responsive-modal'
import { toastr } from 'react-redux-toastr'
import { messagesService } from '../../../_services/messages.service'
import ReactSelect from 'react-select'
import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai"
import { Formik, Field, getIn, ErrorMessage } from 'formik'
import * as Yup from 'yup'
import ErrorPopUp from '../../Shared/ErrorPopUp'
import messageTypes from '../../../_constants/messageTypes'




const MessageModal = (props) => {

  const [loading, setLoading] = useState(false)
  const [data, setData] = useState(props.data)
  const [open, setOpen] = useState(props.open)
  const [isOpen, setIsOpen] = useState(false)
  const [photoIndex, setPhotoIndex] = useState(0)

  useEffect(() => {
    setOpen(props.open)
    setData(props.data)
  }, [props.open, props.data])

  const handleSubmit = (values, setSubmitting) => {
console.log(JSON.stringify(values));

    setLoading(true)

    if (values.id) {

      messagesService.edit(values.id, JSON.stringify(values))
        .then((res) => {

          toastr.success('ویرایش پیام', 'پیام "' + values.title + '" با موفقیت ویرایش شد');
          setLoading(false);
          props.handleModalCloseAndReload();
          setSubmitting(false);

        })
        .catch((error) => {
          console.log(error);
          setLoading(false);
          setSubmitting(false);
          toastr.error('مشکلی پیش آمد. لطفا دوباره سعی کنید.');
        });

    } else {

      messagesService.add(JSON.stringify(values))
        .then((res) => {

          toastr.success('ثبت پیام جدید', 'پیام "' + res.data.title + '" با موفقیت ثبت شد');
          setLoading(false);
          props.handleModalCloseAndReload();
          setSubmitting(false);

        })
        .catch((error) => {
          console.log(error);
          setLoading(false);
          setSubmitting(false);
          toastr.error('مشکلی پیش آمد. لطفا دوباره سعی کنید.');
        });

    }

  }

  const customStyles = {
    control: (provided) => ({
      ...provided,
      background: '#f6f6f6',
      borderRadius: 20,
      boxShadow: 'none',
      border: 'none',
      paddingRight: '12',
    }),
    indicatorSeparator: () => ({
      display: 'none'
    }),

  }

  const ValidationSchema = Yup.object().shape({

    title: Yup.string().nullable().trim().required('عنوان پیام'),
    body: Yup.string().nullable().trim().required('متن پیام'),
    message_type: Yup.string().nullable().trim().required('نوع ارسال'),

  });

  return (
    <Modal open={open} onClose={props.handleModalClose()} center
      classNames={{
        overlay: "customOverlay",
        modal: "customModal lg",
        closeButton: "customModalCloseButton",
      }}>

      <Formik
        enableReinitialize
        initialValues={data}
        onSubmit={(values, { setSubmitting }) => {
          // console.log(values);
          handleSubmit(values, setSubmitting)
        }}
        validationSchema={ValidationSchema}
      >
        {props => {
          const {
            values,
            touched,
            errors,
            isSubmitting,
            handleChange,
            handleBlur,
            setFieldValue,
            handleSubmit,
          } = props;
          return (

            <form onSubmit={handleSubmit}>
              <div className="row" style={{ position: "relative" }}>
                <div className="col-md-12 message-details-modal">

                  <fieldset className="fieldset">
                    <legend>
                      {data && data.id ? 'ویرایش پیام' : 'ارسال پیام جدید'}
                    </legend>

                    <div className="row">
                      <div className="form-inline">

                        <div className="col-md-12">
                          <label>عنوان پیام</label>
                          <Field type="text"
                            value={values.title}
                            onBlur={handleBlur}
                            name="title"
                            autoFocus
                            className={getIn(touched, "title") ? getIn(errors, "title") ? "text-input has-error" : "text-input" : "text-input has-error"}
                          />
                          <ErrorMessage component={ErrorPopUp} name="title" />
                        </div>

                        <div className="col-md-12">
                          <label>متن پیام</label>
                          <Field name="body" rows="10"
                            render={({ field }) =>
                              <textarea {...field}
                                value={values.body}
                                className={getIn(touched, "body") ? getIn(errors, "body") ? "textarea rtl has-error" : "textarea rtl" : "textarea rtl has-error"}
                              />}
                          />
                          <ErrorMessage component={ErrorPopUp} name="body" />
                        </div>

                        <div className="col-md-6">
                          <label>نوع ارسال</label>
                          <ReactSelect
                            name="message_type"
                            placeholder=""
                            styles={customStyles}
                            value={messageTypes.filter(el => el.value === values.message_type)[0] || null}
                            options={messageTypes}
                            onChange={(val) => {
                              setFieldValue('message_type', val.value);
                            }}
                          />
                          <ErrorPopUp error={errors.message_type} touched={touched.message_type} style={{ top: -4, left: 15, right: 'auto' }} />
                        </div>

                        <div className="col-md-12">
                          <br />
                          <button type="submit" className={"icon button submit-btn " + (isSubmitting ? "isSubmitting" : "")} disabled={isSubmitting}>
                            {isSubmitting ?
                              <AiOutlineLoading className="mdi-spin" size="22px" />
                              :
                              <AiOutlineSave size="22px" />
                            }ذخیره</button>
                        </div>

                      </div>
                    </div>

                  </fieldset>

                </div>
              </div>

            </form>

          );
        }}
      </Formik>
    </Modal>

  )
}

export default MessageModal;