import React, { Component, useCallback, useMemo, useState, useRef, useEffect } from 'react'
import { toastr } from 'react-redux-toastr'
import { history } from '../../_helpers'

import { Route, Link } from 'react-router-dom'
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import { Formik, Field, getIn } from 'formik'
import * as Yup from 'yup'
import ErrorPopUp from '../Shared/ErrorPopUp'
import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai";
import { userService } from '../../_services/user.service'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`nav-tabpanel-${index}`}
      aria-labelledby={`nav-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

// TabPanel.propTypes = {
//   children: PropTypes.node,
//   index: PropTypes.any.isRequired,
//   value: PropTypes.any.isRequired,
// };

const useStyles = makeStyles(theme => ({

  topbar: {
    background: "#67b65d",
    color: "#fff",
  },
  menuButton: {
    color: "#fff"
  }
}))



const ResetPassword = (props) => {

  const [loading, setLoading] = useState(false);
  const [data, setData] = useState({
    old_password: '',
    password: '',
    confirmPassword: '',
  })

  const handleSubmit = (values) => {

    setLoading(true)

    let data = values;

    console.log(JSON.stringify(data));

    userService.changePassword(JSON.stringify(data))
      .then((res) => {
        console.log(res);

        setLoading(false)
        // history.push('/')
        toastr.success('ثبت شهرستان', 'کلمه عبور جدید شما با موفقیت ذخیره شد');
        // history.push("/admin/cities");
      }).catch((error) => {
        console.log(error);
        setLoading(false)
      });

  }

  const classes = useStyles()
  const ValidationSchema = Yup.object().shape({
    old_password: Yup.string().trim().nullable().required('کلمه عبور قبلی'),

    password: Yup.string().nullable()
      // .matches(/^(?=.*[0-9])/, { message: "کلمه عبور باید شامل موارد زیر باشد", excludeEmptyString: false })
      // .min(4, 'طول کلمه عبور حداقل باید 4 کاراکتر باشد')
      .trim().nullable().required('وارد کردن کلمه عبور جدید الزامی است'),

    // changePass: Yup.bool(),
    confirmPassword: Yup.string().nullable()
      .oneOf([Yup.ref('password')], 'تکرار کلمه عبور صحیح نیست')
      .trim().nullable().required('تکرار کلمه عبور جدید الزامی است'),

  });

  return (
    <React.Fragment>
      <AppBar position="static" color="default" className={classes.appbar}>
        <Toolbar className={classes.topbar}>
          <IconButton className={classes.menuButton} color="default" aria-label="Menu">
            <Route path="/customer" >
              {({ match }) => <Link to={match ? "/customer" : "/technician"}><NavigateBeforeIcon style={{ color: '#fff' }} /></Link>}
            </Route>

          </IconButton>
          <Typography
            variant="inherit"
            color="inherit"
            className={classes.logo}
          // onClick={() => history.push("home")}
          >
            {/* <img className={styles.imgLogo} src={logo} alt="Logo" />{" "} */}
            {/* <span className={styles.txtLogo}>{appconf('NAME')}</span> */}
          </Typography>
          <Typography
            variant="inherit"
            color="inherit"
            className="appbar-title">
            تغییر کلمه عبور
          </Typography>
        </Toolbar>
      </AppBar>
      <div className="reset-password">


        <Formik
          initialValues={data}
          onSubmit={(values, { setSubmitting }) => {

            handleSubmit(values);
            setSubmitting(false);
          }}
          validationSchema={ValidationSchema}
        >
          {props => {
            const {
              values,
              touched,
              errors,
              // dirty,
              setFieldValue,
              // setFieldTouched,
              // isSubmitting,
              handleChange,
              handleBlur,
              handleSubmit,
              // handleReset,
              // validateForm
            } = props;
            return (
              <div className="col-xs-12">
                <div className="row">
                  <form onSubmit={handleSubmit}>
                    <div className="form-inline">

                      <div className="col-lg-12 col-sm-12">
                        <label>کلمه عبور قبلی</label>
                        <Field type="password"
                          autoComplete="off"
                          id="old_password"
                          name="old_password"
                          value={values.old_password}
                          className={
                            getIn(props.errors, "old_password") &&
                              getIn(props.touched, "old_password")
                              ? "text-input has-error"
                              : "text-input required"
                          }
                        />
                        <ErrorPopUp error={props.errors.old_password} touched={props.touched.old_password} style={{ top: 16 }} />
                      </div>

                      <div className="clearfix"></div>

                      <div className="col-lg-12 col-sm-12">
                        <label>کلمه عبور جدید</label>
                        <Field type="password"
                          autoComplete="off"
                          id="password"
                          name="password"
                          value={values.password}
                          className={
                            getIn(props.errors, "password") &&
                              getIn(props.touched, "password")
                              ? "text-input has-error"
                              : "text-input required"
                          }
                        />
                        <ErrorPopUp error={props.errors.password} touched={props.touched.password} style={{ top: 16 }} />
                      </div>

                      <div className="clearfix"></div>

                      <div className="col-lg-12 col-sm-12">
                        <label>تکرار کلمه عبور جدید</label>
                        <Field type="password"
                          name="confirmPassword"
                          className={
                            getIn(props.errors, "confirmPassword") &&
                              getIn(props.touched, "confirmPassword")
                              ? "text-input has-error"
                              : "text-input required"
                          }
                        />
                        <ErrorPopUp error={props.errors.confirmPassword} touched={props.touched.confirmPassword} style={{ top: 16 }} />
                      </div>

                      <div className="clearfix"></div>
                      <br />

                      <div className="col-md-12">
                        <button type="submit" className={"btn btn-success btn-block " + (loading ? "isSubmitting" : "")} disabled={loading} style={{ borderRadius: 25, height: 40 }}>
                          {loading ?
                            <AiOutlineLoading className="mdi-spin" size="22px" style={{ marginLeft: 5 }} />
                            :
                            <AiOutlineSave size="22px" style={{ marginLeft: 5 }} />
                          }
                        ذخیره
                  </button>
                      </div>



                    </div>
                  </form>
                </div>

                {/* {this.state.loading ? <Loading /> : ''} */}
              </div>
            );
          }}
        </Formik>


      </div>
      {/* <Loading loading={initLoading} /> */}

    </React.Fragment >
  );

}

export default ResetPassword;
