export const userActions = {
  login,
  logout,
  changeUserStatus,
};

function login(dto) {
  return dispatch => {

    userService.login(JSON.stringify(dto))
      .then((res) => {
        // console.log(res);

        let data = res;
        data["newLoggedIn"] = true;

        let status = data.status;
        let role = data.role;
        // console.log(data);

        dispatch(success(data));

        // askForPermissionToReceiveNotifications()
        


        localStorage.setItem('user', JSON.stringify(data));
        // dispatch(userActions.activate(res.data));
        if (status === 0) {

          if (role === 0) {
            history.push('/admin')
          } else if (role === 1) {
            history.push('/technician')
          } else if (role === 2) {
            history.push('/customer')
          }

        } else if (status === 1) {
          toastr.warning("دسترسی شما به سیستم فعال نیست")
          // setSubmitting(false);
          history.push('/')
        }

        // setSubmitting(false);


      })
      .catch((error) => {
        console.log(error);
        // setSubmitting(false);
        toastr.warning("اطلاعات کاربری وارد شده صحیح نمی باشد")
      });

    //     userService.login(username, password)
    //     .then(
    //       user => {
    //         if (user) {
    // // console.log(user);
    //           dispatch(success(user));
    //         }else{
    //           dispatch(failure('اطلاعات وارد شده صحیح نمی باشد'));
    //           dispatch(alertActions.error('اطلاعات وارد شده صحیح نمی باشد'));
    //           toastr.warning('', 'اطلاعات وارد شده صحیح نمی باشد')
    //         }

    //         //history.push('/');
    //       },
    //       error => {
    //         dispatch(failure(error.toString()));
    //         dispatch(alertActions.error(error.toString()));
    //         // toastr.warning('مشکلی پیش آمد', error.toString())
    //       }
    //     );
  };

  function request(user) { return { type: userConstants.LOGIN_REQUEST, user } }
  function success(user) { return { type: userConstants.LOGIN_SUCCESS, user } }
  function failure(error) { return { type: userConstants.LOGIN_FAILURE, error } }
}

function logout() {
  userService.logout();
  return { type: userConstants.LOGOUT };
}

