import React, { useState, useEffect } from 'react'
import NumberFormat from 'react-number-format'
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Dialog from '@material-ui/core/Dialog';
import Divider from '@material-ui/core/Divider';
import DoneAll from '@material-ui/icons/DoneAll';

import { default as ServiceItem } from '../services/Item'

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    direction: 'rtl',
    textAlign: 'right',

  },
  inline: {
    display: 'inline',
  },
}));

function NumberFormatCustom(props) {
  const { inputRef, onChange, ...other } = props;

  return (
    <NumberFormat
      {...other}
      getInputRef={inputRef}
      onValueChange={values => {
        onChange({
          target: {
            value: values.value,
          },
        });
      }}
      thousandSeparator
      isNumericString
    // prefix="$"
    />
  );
}

NumberFormatCustom.propTypes = {
  inputRef: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
};

const NewServicesModal = (props) => {
  const { data, onClose, open, ...other } = props;
  const [parts, setParts] = useState(props.data ? data.parts : [{ name: '', qty: 1, price: '', totalPrice: '' }])
  const classes = useStyles();
  useEffect(() => {
    props.data && setParts(props.data)
  }, [props.data])

  const handleAddRow = () => {

    var newParts = [...parts];
    var newPart = { name: '', qty: 1, price: '', totalPrice: '' };
    newParts.push(newPart);
    setParts(newParts);

  }

  const handleClose = () => {
    onClose()
  };

  return (

    <Dialog
      fullWidth={true}
      onClose={handleClose}
      // onEntering={handleEntering}
      aria-labelledby="confirmation-dialog-title"
      open={open}
      {...other}
    >
      <DialogTitle id="confirmation-dialog-title">سرویس های جدید</DialogTitle>
      <DialogContent dividers>
        <div className="orders" style={{ height: '100%' }}>
          <List className={classes.root}>
            {data && data.map((item, index) => (
              <React.Fragment key={item.id}>

                <ServiceItem data={item} onItemClick={() => { }} />

              </React.Fragment>
            ))}
          </List>
        </div>
      </DialogContent>
      <DialogActions>
        <Button onClick={() => handleClose()} variant="contained" color="primary" size="large">
          بستن
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default NewServicesModal;