import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText';
import { GoCalendar } from 'react-icons/go'

const useStyles = makeStyles(theme => ({
  listItem: {
    margin: 0,
    padding: 10,
  },
}))

const ProjectItem = ({ data, onClickItem }) => {


  const classes = useStyles()

  return (
    data ?
      <div>
        <div className="first-row">
          <span>
            {data.status === 0 ? <span className="messageStatus0">پیام جدید</span> : ''}

          </span>
          <span> </span>
          <span>
            <GoCalendar size="16px" />
            <span>{data.created_at}</span>
          </span>
        </div>
        <ListItem button className={classes.listItem} onClick={() => {
          onClickItem(data.id)
          // console.log(data);
          // let newData = data;

          // newData["open"] = data.open ? !data.open : true
          // setData(newData)
          // setTotal(newData.length)

        }}
        >

          <ListItemText
            className="message-title"
            primary={data.title}

          />
          {/* {data.open ? <ExpandLess /> : <ExpandMore />} */}
        </ListItem>
        {/* <div className="last-row">
                <span>متراژ: {data.area} متر</span>
                <span>مبلغ کل:&nbsp;
                <NumberFormat thousandSeparator={true}
                        className="numberFa"
                        value={data.totalPrice}
                        displayType="text"
                        suffix=" ریال"
                    />
                </span>
            </div> */}
        {/* <Collapse in={data.open} timeout="auto" unmountOnExit>
      <List component="div" disablePadding>
        <ListItem button className={classes.nested}>
          test
        </ListItem>
      </List>
    </Collapse> */}
      </div>
      : ''
  )
}

export default ProjectItem