import React, { PureComponent } from 'react'
import { Field, ErrorMessage, getIn } from "formik"
import ErrorPopUp from '../../Shared/ErrorPopUp'
import ElevatorTypeSelect from '../../Shared/ElevatorTypeSelect'
import NumberFormat from 'react-number-format'
import UsageSelect from '../../Shared/UsageSelect'
import EnginRoomSelect from '../../Shared/EnginRoomSelect'
import PanelTypeSelect from '../../Shared/PanelTypeSelect'
import FeedbackSelect from '../../Shared/FeedbackSelect'
import EngineTypeSelect from '../../Shared/EngineTypeSelect'
import StandardTypeSelect from '../../Shared/StandardTypeSelect'
import CarCommunicationSelect from '../../Shared/CarCommunicationSelect'
import DatePicker from '../../Shared/DatePicker'
import InsuranceSelect from '../../Shared/InsuranceSelect'
import StandardSelect from '../../Shared/StandardSelect'
import SuspensionTypeSelect from '../../Shared/SuspensionTypeSelect'
import DoorTypeSelect from '../../Shared/DoorTypeSelect'
import EmergencySystemSelect from '../../Shared/EmergencySystemSelect'


class Elevator extends PureComponent {



  render() {

    const { touched, errors, setFieldValue, handleBlur, values, index, data } = this.props;

    return (
      <React.Fragment>

        <fieldset className="fieldset">
          <legend>مشخصات آسانسور</legend>

          <div className="col-md-1"></div>

          <div className="col-md-2">
            <label>شناسه ملی</label>
            <input type="text"
              defaultValue={data.serial_number}
              onChange={(e) => {
                setFieldValue(`elevators[${index}][serial_number]`, e.target.value)
              }}
              className={getIn(touched, "serial_number") ? getIn(errors, "serial_number") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].serial_number`} />
          </div>

          <div className="col-md-2">
            <label>نوع آسانسور</label>
            <ElevatorTypeSelect clearable
              defaultValue={data.elevator_type}
              name={`elevators[${index}][elevator_type]`}
              onChange={(val) => {
                // console.log(val);

                setFieldValue(`elevators[${index}][elevator_type]`, val)
              }}

              className={getIn(errors, "elevator_type") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].elevator_type`} />
          </div>

          <div className="col-md-2">
            <label>کاربری</label>
            <UsageSelect clearable
              defaultValue={data.usage}
              onChange={(val) => {
                setFieldValue(`elevators[${index}][usage]`, val)
              }}

              className={getIn(errors, "usage") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].usage`} />
          </div>

          <div className="col-md-2">
            <label>ظرفیت</label>
            <NumberFormat allowNegative={false}
              value={data.capacity}
              onChange={(e) => {
                setFieldValue(`elevators[${index}][capacity]`, parseInt(e.target.value, 0))
              }}
              onBlur={handleBlur}
              className={getIn(touched, "capacity") ? getIn(errors, "capacity") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].capacity`} />
          </div>

          <div className="col-md-2">
            <label>تعداد طبقات</label>
            <NumberFormat allowNegative={false}
              value={data.floors}
              onChange={(e) => {
                setFieldValue(`elevators[${index}][floors]`, parseInt(e.target.value, 0))
              }}
              onBlur={handleBlur}
              className={getIn(touched, "floors") ? getIn(errors, "floors") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].floors`} />
          </div>

          <div className="col-md-1"></div>

          <div className="clearfix"></div>

          <div className="col-md-1"></div>

          <div className="col-md-2">
            <label>نوع درب کابین</label>
            <DoorTypeSelect clearable
              defaultValue={data.door_type}
              name={`elevators[${index}][door_type]`}
              onChange={(val) => {
                setFieldValue(`elevators[${index}][door_type]`, val)
              }}

              className={getIn(errors, "door_type") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].door_type`} />
          </div>

          <div className="col-md-2">
            <label>تعداد توقف</label>
            <NumberFormat allowNegative={false}
              defaultValue={data.stops}
              // value={data.stops}
              name={`elevators[${index}][stops]`}
              onChange={(e) => {
                setFieldValue(`elevators[${index}][stops]`, e.target.value)
              }}
              onBlur={handleBlur}
              className={getIn(touched, "stops") ? getIn(errors, "stops") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].stops`} />
          </div>

          <div className="col-md-2">
            <label>تعداد درب لولایی</label>
            <NumberFormat allowNegative={false}
              defaultValue={data.swing}
              // value={data.swing}
              name={`elevators[${index}][swing]`}
              onChange={(e) => {
                setFieldValue(`elevators[${index}][swing]`, e.target.value)
              }}
              onBlur={handleBlur}
              className={getIn(touched, "swing") ? getIn(errors, "swing") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].swing`} />
          </div>

          <div className="col-md-2">
            <label>تعداد درب اتوماتیک</label>
            <NumberFormat allowNegative={false}
              defaultValue={data.automatic}
              // value={data.automatic}
              name={`elevators[${index}][automatic]`}
              onChange={(e) => {
                setFieldValue(`elevators[${index}][automatic]`, e.target.value)
              }}
              onBlur={handleBlur}
              className={getIn(touched, "automatic") ? getIn(errors, "automatic") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].automatic`} />
          </div>

          <div className="col-md-2">
            <label>نام درب اتوماتیک</label>
            <Field type="text"
              value={data.door_name}
              onBlur={handleBlur}
              name={`elevators[${index}][door_name]`}
              className={getIn(touched, "door_name") ? getIn(errors, "door_name") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].door_name`} />
          </div>

          <div className="col-md-1"></div>

          <div className="clearfix"></div>

          <div className="col-md-1"></div>

          <div className="col-md-2">
            <label>موتورخانه</label>
            <EnginRoomSelect clearable
              defaultValue={data.engine_room}
              name={`elevators[${index}][engine_room]`}
              onChange={(val) => {
                // console.log(val);

                setFieldValue(`elevators[${index}][engine_room]`, val)
              }}

              className={getIn(errors, "engine_room") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].engine_room`} />
          </div>

          <div className="col-md-2">
            <label style={{ fontSize: ".85em" }}>سیستم تعلیق/موقعیت جک</label>
            
            <SuspensionTypeSelect clearable
              defaultValue={data.suspension_type}
              name={`elevators[${index}][suspension_type]`}
              type={data.elevator_type}
              onChange={(val) => {
                setFieldValue(`elevators[${index}][suspension_type]`, val)
              }}
              className={getIn(errors, "suspension_type") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
           
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].suspension_type`} />
          </div>

          <div className="col-md-2">
            <label>نوع موتور</label>
            <EngineTypeSelect clearable
              defaultValue={data.engine_type}
              name={`elevators[${index}][engine_type]`}
              onChange={(val) => {
                // console.log(val);

                setFieldValue(`elevators[${index}][engine_type]`, val)
              }}

              className={getIn(errors, "engine_type") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].engine_type`} />
          </div>

          <div className="col-md-2">
            <label>مدل موتور</label>
            <Field type="text"
              value={data.engine}
              onBlur={handleBlur}
              name={`elevators[${index}][engine]`}
              className={getIn(touched, "engine") ? getIn(errors, "engine") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].engine`} />
          </div>

          <div className="col-md-2">
            <label>توان موتور</label>
            <NumberFormat allowNegative={false}
              defaultValue={data.power}
              // value={data.power}
              name={`elevators[${index}][power]`}
              onChange={(e) => {
                setFieldValue(`elevators[${index}][power]`, e.target.value)
              }}
              onBlur={handleBlur}
              className={getIn(touched, "power") ? getIn(errors, "power") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].power`} />
          </div>

          <div className="col-md-1"></div>

          <div className="clearfix"></div>

          <div className="col-md-1"></div>

          <div className="col-md-2">
            <label>نوع تابلو فرمان</label>
            <PanelTypeSelect clearable
              defaultValue={data.panel_type}
              name={`elevators[${index}][panel_type]`}
              onChange={(val) => {
                // console.log(val);

                setFieldValue(`elevators[${index}][panel_type]`, val)
              }}

              className={getIn(errors, "panel_type") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].panel_type`} />
          </div>

          <div className="col-md-2">
            <label>نام تابلو</label>
            <Field type="text"
              value={data.panel_name}
              onBlur={handleBlur}
              name={`elevators[${index}][panel_name]`}
              className={getIn(touched, "panel_name") ? getIn(errors, "panel_name") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].panel_name`} />
          </div>

          <div className="col-md-2">
            <label>نام درایو</label>
            <Field type="text"
              value={data.drive}
              onBlur={handleBlur}
              name={`elevators[${index}][drive]`}
              className={getIn(touched, "drive") ? getIn(errors, "drive") ? "text-input has-error" : "text-input" : "text-input has-error"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].drive`} />
          </div>

          <div className="col-md-2">
            <label>feedback</label>
            <FeedbackSelect clearable
              defaultValue={data.feedback}
              name={`elevators[${index}][feedback]`}
              onChange={(val) => {
                // console.log(val);

                setFieldValue(`elevators[${index}][feedback]`, val)
              }}

              className={getIn(errors, "feedback") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].feedback`} />
          </div>

          <div className="col-md-2">
            <label>سیستم نجات اضطراری</label>
            <EmergencySystemSelect clearable
              defaultValue={data.emergency_system}
              name={`elevators[${index}][emergency_system]`}
              onChange={(val) => {
                // console.log(val);

                setFieldValue(`elevators[${index}][emergency_system]`, val)
              }}

              className={getIn(errors, "emergency_system") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].emergency_system`} />
          </div>

          <div className="col-md-1"></div>

          <div className="clearfix"></div>

          <div className="col-md-1"></div>

          <div className="col-md-2">
            <label>سیستم ارتباط کابین</label>
            <CarCommunicationSelect clearable
              defaultValue={data.car_communication}
              name={`elevators[${index}][car_communication]`}
              onChange={(val) => {
                // console.log(val);

                setFieldValue(`elevators[${index}][car_communication]`, val)
              }}

              className={getIn(errors, "car_communication") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].car_communication`} />
          </div>

          <div className="col-md-2">
            <label style={{fontSize: '.95em', display: 'block'}}>سرعت آسانسور (ms)</label>
            <Field type="text"
              value={data.speed}
              onBlur={handleBlur}
              name={`elevators[${index}][speed]`}
              className={getIn(touched, "speed") ? getIn(errors, "speed") ? "text-input has-error" : "text-input" : "text-input has-error"}
            /> 
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].speed`} />
          </div>

        </fieldset>
        <fieldset className="fieldset">
          <legend>مشخصات بیمه و استاندارد</legend>

          <div className="col-md-3">
            <label>شرکت بیمه</label>
            <InsuranceSelect clearable
              defaultValue={data.insurance_id}
              name={`elevators[${index}][insurance_id]`}
              onChange={(val) => {
                // console.log(val);

                setFieldValue(`elevators[${index}][insurance_id]`, val)
              }}

              className={getIn(errors, "insurance_id") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].insurance_id`} />
          </div>

          <div className="col-md-3">
            <label>تاریخ شروع بیمه</label>
            <DatePicker 
              name={`elevators[${index}][insurance_date]`}
              defaultValue={data.insurance_date}
              className={getIn(errors, "insurance_date") ? "text-input has-error" : "text-input"}
              onChange={(val) => {
                setFieldValue(`elevators[${index}][insurance_date]`, val);
              }} />
            <ErrorPopUp error={errors.insurance_date} touched={touched.insurance_date} />
          </div>

          <div className="col-md-3">
            <label>تاریخ اتمام بیمه</label>
            <DatePicker 
              name={`elevators[${index}][insurance_finish_date]`}
              defaultValue={data.insurance_finish_date}
              className={getIn(errors, "insurance_finish_date") ? "text-input has-error" : "text-input"}
              onChange={(val) => {
                setFieldValue(`elevators[${index}][insurance_finish_date]`, val);
              }} />
            <ErrorPopUp error={errors.insurance_finish_date} touched={touched.insurance_finish_date} />
          </div>

          <div className="clearfix"></div>


          <div className="col-md-3">
            <label>شرکت استاندارد</label>
            <StandardSelect clearable
              defaultValue={data.standard_id}
              name={`elevators[${index}][standard_id]`}
              onChange={(val) => {

                setFieldValue(`elevators[${index}][standard_id]`, val)
              }}

              className={getIn(errors, "standard_id") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].standard_id`} />
          </div>

          <div className="col-md-3">
            <label>تاریخ اتمام استاندارد</label>
            <DatePicker 
              name={`elevators[${index}][standard_finish_date]`}
              defaultValue={data.standard_finish_date}
              className={getIn(errors, "standard_finish_date") ? "text-input has-error" : "text-input"}
              onChange={(val) => {
                setFieldValue(`elevators[${index}][standard_finish_date]`, val);
              }} />
            <ErrorPopUp error={errors.standard_finish_date} touched={touched.standard_finish_date} />
          </div>

          <div className="col-md-3">
            <label>نوع استاندارد</label>
            <StandardTypeSelect clearable
              defaultValue={data.standard_type}
              name={`elevators[${index}][standard_type]`}
              onChange={(val) => {
                // console.log(val);

                setFieldValue(`elevators[${index}][standard_type]`, val)
              }}

              className={getIn(errors, "standard_type") ? "semantic-select-box has-error" : "semantic-select-box"}
            />
            <ErrorMessage component={ErrorPopUp} name={`elevators[${index}].standard_type`} />
          </div>


        </fieldset>

      </React.Fragment>
    )
  }
}

export default Elevator;