import React from 'react'
import { servicesService } from '../../../_services/services.service'
import Button from '@material-ui/core/Button';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Dialog from '@material-ui/core/Dialog';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';


const CreateServiceModal = (props) => {
  const { data, contractId, onClose, open, ...other } = props;
  const [value, setValue] = React.useState('');
  const radioGroupRef = React.useRef(null);

  // React.useEffect(() => {
  // 	if (!open) {
  // 		setValue(valueProp);
  // 	}
  // }, [valueProp, open]);

  // const handleEntering = () => {
  // 	if (radioGroupRef.current != null) {
  // 		radioGroupRef.current.focus();
  // 	}
  // };

  const handleClose = () => {
    onClose();
  };

  const handleOk = () => {

    let dto = {};
    dto["contract_id"] = props.contractId;
    dto["reason_id"] = value;

    console.log(dto);


    servicesService.request_repair(JSON.stringify(dto))
      .then((res) => {

        console.log(res);

        props.onCloseAndReload()
        // history.push('/installer/projects')
        // toastr.success('ثبت شهرستان  جدید', 'شهرستان  جدید  "' + res.data.cityName + ' ' + res.data.lastName + '" با موفقیت ثبت شد');
        // history.push("/admin/cities");
      })
      .catch((error) => {
        console.log(error);
        onClose();
      });


  };

  const handleChange = event => {
    console.log(event.target.value);

    setValue(event.target.value);
  };

  return (
    <Dialog
      fullScreen={true}
      onClose={handleClose}
      // onEntering={handleEntering}
      aria-labelledby="confirmation-dialog-title"
      open={open}
      {...other}
    >
      <DialogTitle id="confirmation-dialog-title">ثبت دلیل خرابی</DialogTitle>
      <DialogContent dividers>
        <RadioGroup
          ref={radioGroupRef}
          aria-label="ringtone"
          name="ringtone"
          value={parseInt(value, 0)}
          onChange={handleChange}
        >
          {data.map(item => (
            <FormControlLabel value={item.id} key={item.id} control={<Radio />} label={item.name} />
          ))}
        </RadioGroup>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleOk} variant="contained" color="primary" style={{ marginLeft: 10 }}>
          ثبت خرابی
        </Button>
        <Button autoFocus onClick={handleClose} variant="outlined" color="secondary">
          لغو
        </Button>
      </DialogActions>
    </Dialog>

  )
}

export default CreateServiceModal;