import axios from 'axios';
import { handleSuccess, handleError } from './axiosHandler';
import { authHeader, authHeaderJson } from '../_helpers/auth-header';

import { config } from '../config';
const base_url = config.apiUrl;

export const userService = {
  login,
  logout,
  getAll,
  getRoles,
  getUserRoles,
  getById,
  update,
  add,
  edit,
  delete: _delete,

  getTechnicians,
  getCustomers,
  changePassword,
};

function login(dto) {

  const url = base_url + 'auth/login';
  // console.log(url, dto);

  return axios.post(url, dto, { headers: { 'Content-Type': 'application/json' } })
    .then(function (response) {
      // localStorage.setItem('user', JSON.stringify(response.data));
      return response.data;
    })
    .catch(function (error) {
      handleError(error)
    });
}

function logout() {
  // remove user from local storage to log user out
  localStorage.removeItem('user');
  localStorage.removeItem('token');
}

function getAll() {
  const url = base_url + 'users';
  // console.log(url);

  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function getRoles() {
  const url = `api/Roles`;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function getUserRoles(id) {
  const url = `api/UserRoles/` + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function add(dto) {
  const url = 'users';
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson() });
}

function changePassword(dto) {
  const url = base_url + 'auth/reset_password';
  console.log(url);

  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson() });
}

function edit(id, dto) {
  const url =  base_url + 'users/' + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.put(url, dto, { headers: authHeaderJson() });
}

function _delete(id) {
  const url = 'users/' + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.delete(url, { headers: authHeader() });
}


function getById(id) {
  const url = base_url + 'users/' + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function update(user) {
  const requestOptions = {
    method: 'PUT',
    headers: { ...authHeader(), 'Content-Type': 'application/json' },
    body: JSON.stringify(user)
  };

  return fetch(`api/users/${user.id}`, requestOptions).then(handleResponse);
}

function getTechnicians() {
  const url = base_url + `user/technicians`;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function getCustomers() {
  const url = base_url + `user/customers`;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}


function handleResponse(response) {
  return response.text().then(text => {
    const data = text && JSON.parse(text);
    if (!response.ok) {
      if (response.status === 401) {
        // auto logout if 401 response returned from api
        logout();
        //location.reload(true);
      }

      const error = (data && data.message) || response.statusText;
      return Promise.reject(error);
    }

    return data;
  });
}
