import React, { PureComponent } from 'react'
import { Dropdown } from 'semantic-ui-react'
import carCommunication from '../../_constants/carCommunication'



class CarCommunicationSelect extends PureComponent {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      defaultValue: this.props.defaultValue,
      data: carCommunication,
    };
  }

  // componentWillMount() {
  //   this.getData();
  // }

  // getData() {

  //   categoriesService.getAll()
  //     .then((res) => {
  //       let data = res.data.map(elm => ({ "text": elm.name, "value": elm.id }))
  //       this.setState({ data: data, loading: false, isSubmitting: false });
  //     }).catch((error) => { console.log(error) });

  // };


  async componentWillReceiveProps(nextProps) {
    if (nextProps.defaultValue !== this.state.defaultValue) {
      this.setState({ defaultValue: nextProps.defaultValue });
    }
  }

  handleChange = (e) => {

    let selected = e;
    this.setState({ selected: selected })
    this.props.onChange(selected)

  }

  render() {

    const { data, loading, defaultValue, disabled } = this.state;
    const { clearable, className } = this.props;

    return (
      <Dropdown
        placeholder=''
        fluid
        search
        selection
        basic
        onChange={(e, data) => this.handleChange(data.value)}
        defaultValue={defaultValue}

        className={className}
        options={data}
        wrapSelection={false}
        selectOnBlur={false}
        searchInput={{ autoFocus: false }}
        loading={loading}
        disabled={disabled}
        clearable={clearable}
      />
    );
  }
}
export default CarCommunicationSelect;
