import React, { useState, useEffect } from 'react'
import { Tabs, Tab } from 'react-bootstrap'
import { Tab0 } from './Tab0'
import { Tab1 } from './Tab1'
import Tab2 from './Tab2'
import Tab3 from './Tab3'
import Tab4 from './Tab4'
import Tab5 from './Tab5'
import { dashboardService } from '../../../../_services/dashboard.service'
// import FirebaseTokenUpdate from '../../../Shared/FirebaseTokenUpdate'

const AdminDashboard = (porps) => {
  const [activeTab, setActiveTab] = useState("dashboard")
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);

  useEffect(() => {

    getData()

    // FirebaseTokenUpdate()

  }, [])

  const getData = async () => {

    setLoading(true)

    dashboardService.getAll()
      .then((res) => {

        let data_ = res.data;

        console.log(data_);

        setData(data_);

        setLoading(false);

      })
      .catch((error) => {

        console.log(error);

        setLoading(false);

      });

  }


  return (
    <Tabs transition={false}
      className="dashboard-tabs"
      activeKey={activeTab}
      onSelect={key => setActiveTab(key)}
    >

      <Tab eventKey="dashboard" title="دشبورد">
        <Tab0 data={data} loading={loading} />
      </Tab>
      <Tab eventKey="statistics" title="آمار">
        <Tab1 data={data} loading={loading} />
      </Tab>
      <Tab eventKey="denied" title="سرویس های رد شده">
        <Tab2 />
      </Tab>
      <Tab eventKey="today" title="سرویس های امروز">
        <Tab3 data={data} loading={loading} />
      </Tab>
      <Tab eventKey="tomorrow" title="سرویس های فردا">
        <Tab4 data={data} loading={loading} />
      </Tab>
      <Tab eventKey="contracts" title="قراردادهای رو به اتمام">
        <Tab5 />
      </Tab>

    </Tabs>
  )
}

export default AdminDashboard;