const messaging = firebase.messaging();


export const ShowPushNotification = () => {
  const [showDialog, setShowDialog] = React.useState(false)
  const [dialogTitle, setDialogTitle] = React.useState('')
  const [dialogText, setDialogText] = React.useState('')

  useEffect(() => {
    // await askForPermissionToReceiveNotifications();
    initalize()
  })

  const initalize = () => {

    try {
      messaging
        .requestPermission()
        .then(function () {
          console.log("Notification permission granted.");
          let newToekn = messaging.getToken()
          FirebaseTokenUpdate(newToekn)
          return newToekn
        })
        .then(function (token) {
          localStorage.setItem("fcm_token", token);
          console.log("token is : " + token)
        })
        .catch(function (err) {
          console.log("Unable to get permission to notify.", err);
        });
  
    } catch (error) {
      console.error(error);
    }

    try {
      // messaging
      //   .requestPermission()
      //   .then(function () {
      //     console.log("Notification permission granted.");
      //     return messaging.getToken()
      //   })
      //   .then(function (token) {
      //     localStorage.setItem("fcm_token", token);
      //     console.log("token is : " + token)
      //   })
      //   .catch(function (err) {
      //     console.log("Unable to get permission to notify.", err);
      //   });

      messaging.onMessage(function (payload) {
        // console.log("Message received. ", payload);
        // console.log(JSON.stringify(payload));
        // alert('[firebase-messaging-sw.js] Received background message ', payload)
        //kenng - foreground notifications
        const { title, body, ...options } = payload.notification;
        setDialogTitle(title)
        setDialogText(body)
        setShowDialog(true)
        navigator.serviceWorker.ready.then(registration => {
          registration.showNotification(title, options);
        });
      });

    } catch (error) {
      console.error(error);
    }

  }

 
  async function FirebaseTokenUpdate(newToekn) {
    
      let fcm_token = localStorage.getItem("fcm_token");
  
      if (fcm_token !== undefined && fcm_token !== null && fcm_token !== "" && fcm_token !== newToekn) {
  
        let dto = { "firebase_token": fcm_token }
  
        // console.log(fcm_token);
  
        await dashboardService.setFirebaseToken(dto)
          .then((res) => {
  
            // let data_ = res.data;
  
            // console.log(data_);
  
          })
          .catch((error) => {
  
            console.log(error);
  
          });
  
      } else {
        // await askForPermissionToReceiveNotifications()
        // FirebaseTokenUpdate()
      }
    
  }


  return (
    <>
      <SweetAlert
        show={showDialog}
        onCancel={() => setShowDialog(false)}
        closeOnClickOutside
        // showCancel
        type={"success"}
        btnSize="sm"
        customclassName="swal2"
        confirmBtnCssClass="btn btn-success swal2-confirm"
        title={dialogTitle}
        onConfirm={() => setShowDialog(false)}
        confirmBtnText="بستن"
      >
        {dialogText}
      </SweetAlert>
    </>
  )
}

