import React, { useState, useRef, useEffect } from 'react'
import { installersService } from '../../../_services/installers.service'
import { FaMedal } from 'react-icons/fa'
import { FiDollarSign, FiLayers } from 'react-icons/fi'
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import { OrdersChart } from './partials/OrdersChart'
import { AiOutlineLoading } from "react-icons/ai"
import Typography from '@material-ui/core/Typography';
import NumberFormat from 'react-number-format'
import { FiBox } from 'react-icons/fi'
import { Route, Link } from 'react-router-dom'


const initData = {
  statistics: {
    totalProjects: 0,
    totalArea: 0,
    totalTransactions: 0,
  },
  chartData: []
}

export const Charts = (props) => {
  const [data, setData] = useState(initData)
  const [page, setPage] = useState(0)
  const [pageSize, setPageSize] = useState(12)
  const [total, setTotal] = useState(0)
  const items = useRef([])
  const [loading, setLoading] = useState(false)
  const [initLoading, setInitLoading] = useState(true)
  const [endReached, setEndReached] = useState(false)

  // the setTimeout below simulates a network request.
  // In the real world, you can fetch data from a service.
  // the setTotal call will trigger a refresh for the list,
  // so make sure you call it last.
  const getData = () => {

    setLoading(true)

    installersService.GetStatistics()
      .then((res) => {

        let newData = res.data;

        if (!newData.chartData.lenght > 0) {
          newData["chartData"] = [{date: "", value: 0, dateShamsi: ""}]
        }
        console.log(newData.chartData)

        setData(newData);

        setLoading(false)

      })
      .catch((error) => {
        console.log(error);
      });

  }

  useEffect(() => {
    getData()
  }, [])


  // const getData = () => {

  //   const { dto } = this.state;

  //   installersService.getAll(JSON.stringify(dto))
  //     .then((res) => {
  //       console.log(res.data)
  //       setData(res.data.data)
  //     })
  //     .catch((error) => {
  //       console.log(error);
  //     });
  // };


  return (
    <React.Fragment>
      <AppBar position="static" color="default" >
          <Toolbar className="topbar-primary">
            <IconButton className="menuButton" color="default" aria-label="Menu">
            <Link to="/customer"><NavigateBeforeIcon /></Link>

            </IconButton>
            <Typography
              variant="inherit"
              color="inherit"
            // onClick={() => history.push("home")}
            >
              {/* <img className={styles.imgLogo} src={logo} alt="Logo" />{" "} */}
              {/* <span className={styles.txtLogo}>{appconf('NAME')}</span> */}
            </Typography>
            <Typography
              variant="inherit"
              color="inherit"
              className="appbar-title">
            نمایش آمار
          </Typography>
        </Toolbar>
      </AppBar>
      <div className="statistics">


        <div className="statistics-info">
          <div className="totalQuantities">
            <div>
              <FiLayers size="25px" />
            </div>
            {loading ?
              <span><AiOutlineLoading className="mdi-spin main-color" size="20px" /></span>
              :
              <span>
                تعداد قرارداد ها:<strong>{data.statistics.totalArea}</strong>
              </span>
            }
          </div>
          <div className="totalOrders">
            <div>
              <FiBox size="25px" />
            </div>
            {loading ?
              <span><AiOutlineLoading className="mdi-spin main-color" size="20px" /></span>
              :
              <span>
                تعداد کل سرویس ها:
                <strong>
                  {data.statistics.totalProjects}
                </strong>
              </span>
            }
          </div>
          <div className="totalScores">
            <div>
              <FaMedal size="18px" />
            </div>
            {loading ?
              <span><AiOutlineLoading className="mdi-spin main-color" size="20px" /></span>
              :
              <span>
                تعداد سرویس های خرابی:<strong>{data.statistics.totalScores}</strong>
              </span>
            }
          </div>
          <div className="totalPrices">
            <div>
              <FiDollarSign size="25px" />
            </div>
            {loading ?
              <span><AiOutlineLoading className="mdi-spin main-color" size="20px" /></span>
              :
              <span>
                جمع مبلغ بدهی شما:
                <strong>
                  <NumberFormat thousandSeparator={true}
                    className="numberFa"
                    value={data.statistics.totalTransactions}
                    displayType="text"
                    suffix=" ریال"
                  />
                </strong>
              </span>
            }
          </div>
        </div>


        <OrdersChart data={data.chartData} />


      </div>
      {/* <Loading loading={loading} /> */}

    </React.Fragment >
  );

}
