import React from "react"
import { Field, ErrorMessage, getIn } from "formik"
import ErrorPopUp from '../../Shared/ErrorPopUp'
import ServiceDaySelect from '../../Shared/ServiceDaySelect'
import CustomerSelect from '../../Shared/CustomerSelect'
import UserSelect from '../../Shared/UserSelect'
import NumberFormat from 'react-number-format'
import PriceInput from '../../Shared/PriceInput'
import DatePicker from '../../Shared/DatePicker'
import PaymentTypeSelect from '../../Shared/PaymentTypeSelect'
import { Tabs, Tab } from 'react-bootstrap'

import Elevator from './Elevator'

const elevator = {
  'serial_number': '',
  'elevator_type': '',
  'usage': '',
  'capacity': '',
  'floors': '',

  'door_type': '',
  'stops': '',
  'swing': '',
  'automatic': '',
  'door_name': '',

  'engine_room': '',
  'suspension_type': '',
  'engine': '',
  'engine_type': '',
  'power': '',

  'panel_type': '',
  'panel_name': '',
  'drive': '',
  'feedback': '',
  'emergency_system': '',

  'car_communication': '',
  'speed': '',

  'insurance_id': '',
  'insurance_date': '',
  'insurance_finish_date': '',

  'standard_id': '',
  'standard_finish_date': '',
  'standard_type': '',
}

export default ({ touched, errors, setFieldValue, values, handleBlur }) => (
  <div className="row display-flex">
    <div className="col-lg-8 col-lg-offset-2 col-md-10 col-md-offset-1 col-sm-12 col-xs-12">

      <div className="row">
        <div className="form-inline">

          <fieldset className="fieldset">
            <legend>مشخصات قرارداد</legend>

            <div className="col-lg-3 col-md-6">
              <label>کد ساختمان</label>
              <Field type="text"
                value={values.building_number}
                onBlur={handleBlur}
                name="building_number"
                autoFocus
                className={getIn(touched, "building_number") ? getIn(errors, "building_number") ? "text-input has-error" : "text-input" : "text-input has-error"}
              />
              <ErrorMessage component={ErrorPopUp} name="building_number" />
            </div>

            <div className="col-lg-3 col-md-3">
              <label>شماره قرارداد</label>
              <Field type="text"
                value={values.contract_number}
                onBlur={handleBlur}
                name="contract_number"
                autoFocus
                className={getIn(touched, "contract_number") ? getIn(errors, "contract_number") ? "text-input has-error" : "text-input" : "text-input has-error"}
              />
              <ErrorMessage component={ErrorPopUp} name="contract_number" />
            </div>

            <div className="clearfix"></div>

            <div className="col-md-3">
              <label>تعداد آسانسور</label>
              <NumberFormat allowNegative={false}
                defaultValue={values.elevators.length || 1}
                value={values.elevators.length}
                name="elevator_count"
                onChange={(e) => {
                  // setFieldValue('elevator_count', e.target.value)
                  if (e.target.value > 1) {
                    let elevators = [];
                    for (let index = 0; index < e.target.value; index++) {
                      elevators.push(elevator)
                    }
                    setFieldValue('elevators', elevators)
                  }
                }}
                onBlur={handleBlur}
                className={getIn(touched, "elevator_count") ? getIn(errors, "elevator_count") ? "text-input has-error" : "text-input" : "text-input has-error"}
              />
              <ErrorMessage component={ErrorPopUp} name="elevator_count" />
            </div>

            <div className="col-md-3">
              <label>تعداد توقف</label>
              <NumberFormat allowNegative={false}
                defaultValue={values.stops}
                value={values.stops}
                name="stops"
                onChange={(e) => {
                  setFieldValue('stops', e.target.value)
                }}
                onBlur={handleBlur}
                className={getIn(touched, "stops") ? getIn(errors, "stops") ? "text-input has-error" : "text-input" : "text-input has-error"}
              />
              <ErrorMessage component={ErrorPopUp} name="stops" />
            </div>

            <div className="col-lg-6 col-md-6">
              <label>آدرس محل نصب آسانسور(ها)</label>
              <Field type="text"
                value={values.address}
                onBlur={handleBlur}
                name="address"
                className={getIn(touched, "address") ? getIn(errors, "address") ? "text-input has-error" : "text-input" : "text-input has-error"}
              />
              <ErrorMessage component={ErrorPopUp} name="address" />
            </div>
            <div className="clearfix"></div>

            <div className="clearfix"></div>

            <div className="col-md-3">
              <label>تاریخ شروع قرارداد</label>
              <DatePicker name="start_date"
                defaultValue={values.start_date}
                className={getIn(errors, "start_date") ? "text-input has-error" : "text-input"}
                onChange={(val) => {
                  setFieldValue('start_date', val);
                }} />
              <ErrorPopUp error={errors.start_date} touched={touched.start_date} />
            </div>

            <div className="col-md-3">
              <label>تاریخ اتمام قرارداد</label>
              <DatePicker name="finish_date" minDate={values.start_date}
                defaultValue={values.finish_date}
                className={getIn(errors, "finish_date") ? "text-input has-error" : "text-input"}
                onChange={(val) => {
                  setFieldValue('finish_date', val);
                }} />
              <ErrorPopUp error={errors.finish_date} touched={touched.finish_date} />
            </div>

            <div className="col-md-3">
              <label>مبلغ کل قرارداد</label>
              <PriceInput
                name="total_price"
                defaultValue={values.total_price}
                value={values.total_price}
                onChange={(val) => {
                  setFieldValue('total_price', val)
                }}
                onBlur={handleBlur}
                className={getIn(touched, "total_price") ? getIn(errors, "total_price") ? "text-input has-error" : "text-input" : "text-input has-error"}
              />
              <ErrorMessage component={ErrorPopUp} name="total_price" />
            </div>

            <div className="col-md-3">
              <label>نحوه پرداخت</label>
              <PaymentTypeSelect clearable
                defaultValue={values.payment_type}
                name="payment_type"
                onChange={(val) => {
                  // console.log(val);

                  setFieldValue('payment_type', val)
                }}

                className={getIn(errors, "payment_type") ? "semantic-select-box has-error" : "semantic-select-box"}
              />
              <ErrorMessage component={ErrorPopUp} name="payment_type" />
            </div>

            <div className="clearfix"></div>

            <div className="col-md-3">
              <label>روز/روزهای سرویس</label>
              <ServiceDaySelect
                defaultValue={values.service_day}
                name="service_day"
                onChange={(val) => {
                  // console.log(val);

                  setFieldValue('service_day', val)
                }}

                className={getIn(errors, "service_day") ? "text-input has-error" : "text-input"}
              />
              <ErrorMessage component={ErrorPopUp} name="service_day" />
            </div>

            <div className="col-md-3">
              <label>انتخاب مشتری</label>
              <CustomerSelect clearable
                defaultValue={values.customer_id}
                name="customer_id"
                onChange={(val) => {
                  // console.log(val);

                  setFieldValue('customer_id', val)
                }}

                className={getIn(errors, "customer_id") ? "semantic-select-box has-error" : "semantic-select-box"}
              />
              <ErrorMessage component={ErrorPopUp} name="customer_id" />
            </div>

            <div className="col-md-3">
              <label>انتخاب سرویس کار</label>
              <UserSelect clearable
                defaultValue={values.user_id}
                name="user_id"
                onChange={(val) => {
                  // console.log(val);

                  setFieldValue('user_id', val)
                }}

                className={getIn(errors, "user_id") ? "semantic-select-box has-error" : "semantic-select-box"}
              />
              <ErrorMessage component={ErrorPopUp} name="user_id" />
            </div>

            <div className="clearfix"></div>

            <div className="col-md-12">
              <label>توضیحات</label>
              <Field name="description" rows="10"
                render={({ field }) =>
                  <textarea {...field}
                    value={values.description}
                    className={getIn(touched, "description") ? getIn(errors, "description") ? "textarea rtl has-error" : "textarea rtl" : "textarea rtl"}
                  />}
              />
              <ErrorMessage component={ErrorPopUp} name="description" />
            </div>

          </fieldset>

          <br />
          <Tabs transition={false}
            id="controlled-tab-example"
            activeKey={values.tabKey}
            onSelect={key => setFieldValue("tabKey", key)}
          >
            {values && values.elevators && values.elevators.map((item, index) => (

              <Tab eventKey={"elevator-" + index} title={"آسانسور " + (index + 1)} key={"elevator-" + index} >
                <Elevator data={item} {...{ touched, errors, setFieldValue, handleBlur }} index={index} />
              </Tab>

            ))}
          </Tabs>

        </div>
      </div>

    </div>

  </div>
);