/*eslint-env jquery*/
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { toastr } from 'react-redux-toastr'
import MySweetAlert from '../../Shared/MySweetAlert'
import Loading from '../../Shared/Loading'
import { contractsService } from '../../../_services/contracts.service'
import { FaPlus } from 'react-icons/fa'
import { FiEdit } from 'react-icons/fi'
import { FiTrash2 } from 'react-icons/fi'
import NumberFormat from 'react-number-format'


// Import React Table
import matchSorter from 'match-sorter'
import ReactTable from "react-table"
import "react-table/react-table.css"


class Contracts extends Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      data: [],
      loading: true,
      confirmDeleteShow: false,
      requiredItem: 0,
      deleteItemIndex: 0,
      model: {}
    }
  }

  componentDidMount() {
    this.getData()
  }

  handleSweetAlertHide = () => {
    this.setState({ confirmDeleteShow: false });
  }

  getData() {
    contractsService.getAll()
      .then((res) => {
        console.log(res.data)
        this.setState({ data: res.data.data, loading: false });
      })
      .catch((error) => {
        console.log(error);
        this.setState({ loading: false });
      })
  };

  handleError(error) {
    this.setState({ loading: false });
    // // toastr.warning('مشکلی پیش آمد', error);
  }

  handleDelete = (e) => {
    let _model = this.state.data[e];

    this.setState({ confirmDeleteShow: true, model: _model });
  }

  handleConfirmDelete = () => {
    this.setState({ loading: true, confirmDeleteShow: false });
    console.log(this.state.model);
    contractsService.delete(this.state.model.id)
      .then((res) => {
        toastr.success('قرارداد با موفقیت حذف شد');
        this.getData();
      })
      .catch((error) => {
        this.handleError(error)
      });
  }

  render() {
    const { data } = this.state;
    return (
      <div>

        {/* <button onClick={askForPermissionToReceiveNotifications} >
      Click here to receive notifications
    </button> */}

        <div className="row table-container" style={{ marginTop: 0 }}>
          <ReactTable
            data={data}
            filterable
            loading={this.state.loading}
            previousText='صفحه قبل'
            nextText='صفحه بعد'
            loadingText='در حال بارگذاری ...'
            noDataText=''
            pageText='صفحه'
            ofText='از'
            rowsText='تعداد نمایش'
            getTableProps={() => ({ style: { display: 'block' } })} // direction: rtl fix

            defaultFilterMethod={(filter, row) =>
              String(row[filter.id]) === filter.value}
            columns={[
              {
                Header: "ردیف",
                id: "row",
                maxWidth: 50,
                filterable: false,
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                className: "numberFa",
                Cell: (row) => {
                  return row.index + 1;
                }
              },
              // {
              //   Header: 'عکس قرارداد',
              //   accessor: 'photoName',
              //   id: "photoName",
              //   width: 100,
              //   sortable: false,
              //   filterable: false,
              //   style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
              //   Cell: row => (
              //     row.original.images && row.original.images.length > 0 ?
              //       <img style={{ height: 70, width: 80, display: 'inline' }} alt='' src={config.uploadsUrl + row.original.images[0]} />
              //       : ''
              //   ),
              // },
              {
                Header: "کد ساختمان",
                accessor: "building_number",
                id: "building_number",
                style: { display: 'flex', alignItems: 'center', overflow: 'visible', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["extraCode"] }),
                filterAll: true,
                Filter: ({ filter, onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,
              },
              {
                Header: "شماره قرارداد",
                accessor: "contract_number",
                id: "contract_number",
                style: { display: 'flex', alignItems: 'center', overflow: 'visible', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["extraCode"] }),
                filterAll: true,
                Filter: ({ filter, onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,
              },
              {
                Header: "مبلغ قرارداد",
                id: "total_price",
                accessor: d => d.total_price,
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                minWidth: 120,
                className: "numberFa",
                filterAll: true,
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["total_price"] }),
                Filter: ({ filter, onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,
                Cell: props => (
                  <NumberFormat thousandSeparator={true}
                    className="numberFa"
                    value={props.original.total_price}
                    displayType="text"
                    suffix="ریال"
                  />
                )
              },
              {
                Header: "نام و نام خانوادگی",
                accessor: "full_name",
                id: "full_name",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["full_name"] }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,
                Cell: row => (
                  row.original.customer && (row.original.customer.full_name)
                )
              },
              {
                Header: "ویرایش",
                accessor: 'id',
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                sortable: false,
                Filter: ({ filter, onChange }) =>
                  <Link to="/admin/contracts/new" className="btn btn-primary react-table-add-btn">
                    <span>
                      <FaPlus size="16px" />
                      <span>افزودن قرارداد جدید</span>
                    </span>
                  </Link>,
                Cell: row => (
                  <div>
                    <Link to={'/admin/contracts/' + row.original.id + '/edit'} params={{ id: row.original.id }} className="btn btn-sm btn-simple btn-success bmd-ripple bmd-flat-btn" rel="tooltip" data-original-title="ویرایش"><FiEdit size="24px" /></Link>
                    <a onClick={() => this.handleDelete(row.index)} className="btn btn-sm btn-simple btn-danger bmd-ripple bmd-flat-btn delete-btn" data-original-title="حذف" rel="tooltip"><FiTrash2 size="24px" /></a>
                  </div>
                )
              }
            ]}
            defaultPageSize={10}
            className="-striped -highlight"
          />

        </div>

        <MySweetAlert
          show={this.state.confirmDeleteShow}
          onCancel={this.handleSweetAlertHide}
          onConfirm={this.handleConfirmDelete}
          title={"آیا از حذف قرارداد  [" + this.state.model.firstName + ' ' + this.state.model.lastName + "] مطمئن هستید؟"}
        >
        </MySweetAlert>

        {this.state.loading ? <Loading /> : ''}
      </div>
    );
  }
}

export default Contracts;
