/*eslint-env jquery*/
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { toastr } from 'react-redux-toastr'
import MySweetAlert from '../../Shared/MySweetAlert'
import Loading from '../../Shared/Loading'
import Select from 'react-select'
import { userService } from '../../../_services/user.service'
import matchSorter from 'match-sorter'
import ReactTable from "react-table"
import "react-table/react-table.css"
import { FaPlus } from 'react-icons/fa'
import { FiEdit } from 'react-icons/fi'
import { FiTrash2 } from 'react-icons/fi'

class Users extends Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      data: [],
      loading: true,
      confirmDeleteShow: false,
      requiredItem: 0,
      deleteItemIndex: 0,
      model: {}
    }

    this.getData()
  }

  handleSweetAlertHide = () => {
    this.setState({ confirmDeleteShow: false });
  }

  getData() {
    userService.getAll()
      .then((res) => {
        console.log(res.data)
        this.setState({ data: res.data, loading: false });
      })
  };

  handleError(error) {
    this.setState({ loading: false });
    // // toastr.warning('مشکلی پیش آمد', error);
  }

  handleDelete = (e) => {
    let _model = this.state.data[e];
    console.log(_model);

    this.setState({ confirmDeleteShow: true, model: _model });
  }

  handleConfirmDelete = () => {
    this.setState({ loading: true, confirmDeleteShow: false });
    // console.log(this.state.model );
    userService.delete(this.state.model.id)
      .then((res) => {
        toastr.success('کاربر با موفقیت حذف شد');
        this.getData();
      })
      .catch((error) => {
        this.handleError(error)
      });
  }

  render() {

    return (
      <div>
        <div className="row table-container" style={{ marginTop: 0 }}>
          <ReactTable
            data={this.state.data}
            filterable
            loading={this.state.loading}
            previousText='صفحه قبل'
            nextText='صفحه بعد'
            loadingText='در حال بارگذاری ...'
            noDataText=''
            pageText='صفحه'
            ofText='از'
            rowsText='تعداد نمایش'
            getTableProps={() => ({ style: { display: 'block' } })} // direction: rtl fix

            defaultFilterMethod={(filter, row) =>
              String(row[filter.id]) === filter.value}
            columns={[
              {
                Header: "ردیف",
                id: "row",
                maxWidth: 50,
                filterable: false,
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                className: "numberFa",
                Cell: (row) => {
                  return row.index + 1;
                }
              },
              {
                Header: "نوع کاربر",
                accessor: "role",
                id: "role",
                style: { display: 'flex', alignItems: 'center', overflow: 'visible', justifyContent: 'center' },

                filterMethod: (filter, row) => {

                  if (filter.value === null || filter.value === undefined) {
                    return true;
                  }
                  if (row[filter.id] === filter.value.value) {
                    return true;
                  }
                  // console.log(filter);
                  //return row[filter.id] === filter.value;
                },
                Cell: row => (
                  <div>
                    {(row.original.role === 0
                      ? 'مدیر سیستم'
                      : row.original.role === 1
                        ? 'سرویسکار'
                        : row.original.role === 2
                          ? 'مشتری'
                          : ''
                    )}
                  </div>
                ),
                Filter: ({ onChange }) =>
                  <Select isClearable
                    placeholder=''
                    options={[
                      { value: 0, label: 'مدیر سیستم' },
                      { value: 1, label: 'سرویسکار' },
                      { value: 2, label: 'مشتری' },
                    ]}

                    onChange={event => onChange(event)}
                  />
              },
              {
                Header: "نام کاربری",
                accessor: "username",
                id: "username",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["username"] }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              {
                Header: "نام و نام خانوادگی",
                accessor: "full_name",
                id: "full_name",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["full_name"] }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              {
                Header: "تلفن",
                accessor: "cell",
                id: "cell",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["cell"] }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              // {
              //   Header: "وضعیت",
              //   accessor: d => d.status,
              //   id: "status",
              //   sortable: false,
              //   style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
              //   width: 140,
              //   Cell: row => (
              //     <div>
              //       {row.original.status ? <FiCheckCircle size="24px" color="#4caf50" /> : <FiCircle size="24px" color="#f44336" />}
              //     </div>
              //   ),
              //   filterMethod: (filter, row) => {

              //     if (filter.value === null || filter.value === undefined) {
              //       return true;
              //     }
              //     return row.status === filter.value.value

              //   },
              //   Filter: ({ onChange }) =>
              //     <Select isClearable
              //       placeholder=""
              //       options={[
              //         { value: 1, label: 'فعال' },
              //         { value: 0, label: 'غیر فعال' },
              //       ]}

              //       onChange={event => onChange(event)}
              //     />
              // },
              {
                Header: "ویرایش",
                accessor: 'id',
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                sortable: false,
                Filter: ({ filter, onChange }) =>
                  <Link to="/admin/users/new" className="btn btn-primary react-table-add-btn">
                    <span>
                      <FaPlus size="16px" />
                      <span>افزودن کاربر جدید</span>
                    </span>
                  </Link>,
                Cell: row => (
                  <div>
                    <Link to={'/admin/users/' + row.original.id + '/edit'} params={{ id: row.original.id }} className="btn btn-sm btn-simple btn-success bmd-ripple bmd-flat-btn" rel="tooltip" data-original-title="ویرایش"><FiEdit size="24px" /></Link>
                    <a onClick={() => this.handleDelete(row.index)} className="btn btn-sm btn-simple btn-danger bmd-ripple bmd-flat-btn delete-btn" data-original-title="حذف" rel="tooltip"><FiTrash2 size="24px" /></a>
                  </div>
                )
              }
            ]}
            defaultPageSize={10}
            className="-striped -highlight"
          />

        </div>

        <MySweetAlert
          show={this.state.confirmDeleteShow}
          onCancel={this.handleSweetAlertHide}
          onConfirm={this.handleConfirmDelete}
          title={"آیا از حذف کاربر  " + (this.state.model.firstName ? this.state.model.firstName : '') + ' ' + (this.state.model.lastName ? this.state.model.lastName : '') + " مطمئن هستید؟"}
        >
        </MySweetAlert>

        {this.state.loading ? <Loading /> : ''}
      </div>
    );
  }
}

export default Users;
