/*eslint-env jquery*/
import React, { Component } from 'react'
import { toastr } from 'react-redux-toastr'
import MySweetAlert from '../../Shared/MySweetAlert'
import Loading from '../../Shared/Loading'
import { denyReasonsService } from '../../../_services/denyReasons.service'
import Modal from 'react-responsive-modal'
import matchSorter from 'match-sorter'
import ReactTable from "react-table"
import { Formik } from 'formik'
import * as Yup from 'yup'
import ErrorPopUp from '../../Shared/ErrorPopUp'
import { FaPlus } from 'react-icons/fa'
import { FiEdit } from 'react-icons/fi'
import { FiTrash2 } from 'react-icons/fi'

class DenyReasons extends Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      initialItems: [],
      data: [],
      modal: false,
      modalHeading: '',
      loading: true,
      confirmDeleteShow: false,
      deleteItemIndex: 0,
      model: { name: '', mode: '' }
    }

    this.getData()
  }

  handleModalCreate = () => {
    this.setState({ modalHeading: 'ثبت دلیل جدید', modal: true, model: { id: 0, name: '', mode: '' } });
  }

  handleModalEdit = (e) => {
    let _model = this.state.data[e];
    
    this.setState({ modal: true, model: _model, modalHeading: 'ویرایش دلیل "' + _model.name + '"' });
  }

  handleModalHide = (e) => {
    var evt = e || window.event; // IE compatibility
    if (evt.preventDefault) {
      evt.preventDefault();
    }
    this.setState({ modal: false });
  }

  handleSweetAlertHide = () => {
    this.setState({ confirmDeleteShow: false });
  }

  getData() {
    denyReasonsService.getAll()
      .then((res) => {
        // console.log(res.data)
        this.setState({ initialItems: res.data, data: res.data, loading: false, isSubmitting: false, modal: false });
      })
  };


  handleError(error) {
    this.setState({ loading: false });
    // // toastr.warning('مشکلی پیش آمد', error);
  }

  handleDelete = (e) => {
    let _model = this.state.data[e];

    this.setState({ confirmDeleteShow: true, model: _model });
  }

  handleConfirmDelete = () => {
    this.setState({ loading: true, confirmDeleteShow: false });

    denyReasonsService.delete(this.state.model.id)
      .then((res) => {
        toastr.success('حذف دلیل', 'دلیل با موفقیت حذف شد');
        this.getData();
      })
      .catch((error) => {
        this.handleError(error)
      });
  }

  handleSubmit = (values) => {
    this.setState({ loading: true });

    if (values.id === 0) {

      denyReasonsService.add(JSON.stringify(values))
        .then((res) => {

          toastr.success('ثبت دلیل جدید', 'دلیل "' + res.data.name + '" با موفقیت ثبت شد');
          this.setState({ modal: false });
          this.getData();

        })
        .catch((error) => {
          this.handleError(error)
        });

    } else {

      denyReasonsService.edit(values.id, JSON.stringify(values))
        .then((res) => {

          toastr.success('ویرایش دلیل', 'دلیل "' + values.name + '" با موفقیت ویرایش شد');
          this.setState({ modal: false });
          this.getData();

        })
        .catch((error) => {
          this.handleError(error)
        });

    }
  }

  render() {

    const ValidationSchema = Yup.object().shape({
      name: Yup.string().trim().required('نام دلیل'),
    });
    const { model, loading } = this.state;
    return (
      <div>
        <div className="row table-container" style={{ marginTop: 0 }}>
          <ReactTable
            data={this.state.data}
            filterable

            previousText='صفحه قبل'
            nextText='صفحه بعد'
            loadingText='در حال بارگذاری ...'
            noDataText='نتیجه ای یافت نشد'
            pageText='صفحه'
            ofText='از'
            rowsText='تعداد نمایش'
            getTableProps={() => ({ style: { display: 'block' } })} // direction: rtl fix

            defaultFilterMethod={(filter, row) =>
              String(row[filter.id]) === filter.value}
            columns={[
              {
                Header: "ردیف",
                id: "row",
                maxWidth: 50,
                filterable: false,
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                className: "numberFa",
                Cell: (row) => {
                  return row.index + 1;
                }
              },
              {
                Header: "نام دلیل",
                accessor: "name",
                id: "name",
                style: { display: 'flex', alignItems: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["name"] }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              {
                Header: "ویرایش",
                accessor: 'id',
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                sortable: false,
                // filterable: false,
                Filter: ({ filter, onChange }) =>
                  <a onClick={this.handleModalCreate} className="btn react-table-add-btn"
                  ><FaPlus size="16px" /> 
                    افزودن دلیل جدید
            </a>,
                Cell: row => (
                  <div>
                    <a onClick={() => this.handleModalEdit(row.index)} className="btn btn-sm btn-simple btn-success bmd-ripple bmd-flat-btn" rel="tooltip" data-original-title="ویرایش"><FiEdit size="24px" /></a>

                    <a onClick={() => this.handleDelete(row.index)} className="btn btn-sm btn-simple btn-danger bmd-ripple bmd-flat-btn delete-btn" data-original-title="حذف" rel="tooltip"><FiTrash2 size="24px" /></a>
                  </div>
                )
              }
            ]}
            defaultPageSize={10}
            className="-striped -highlight"
          />

        </div>
        <Modal open={this.state.modal} onClose={this.handleModalHide} center
          classNames={{
            overlay: "customOverlay",
            modal: "customModal",
            closeButton: "customModalCloseButton",
          }}>

          <Formik
            initialValues={model}
            onSubmit={(values, { setSubmitting }) => {
              // console.log(values);
              this.handleSubmit(values)
            }}
            validationSchema={ValidationSchema}
          >
            {props => {
              const {
                values,
                touched,
                errors,
                isSubmitting,
                handleChange,
                handleBlur,
                handleSubmit,
              } = props;
              return (

                <div className="row" style={{ position: "relative" }}>
                  <div className="col-md-12">
                    <form onSubmit={handleSubmit}>

                      <div className="row">
                        <div className="form-inline">
                          <div className="col-md-12">
                            <label>نام دلیل</label>
                          </div>
                          <div className="col-md-12">
                            <input autoFocus type="text" id="name" name="name"
                              className={
                                errors.name && touched.name ? 'text-input has-error' : 'text-input'
                              }
                              onChange={handleChange}
                              onBlur={handleBlur} value={values.name} />
                            <ErrorPopUp error={errors.name} touched={touched.name} />
                          </div>

                          <div className="col-md-12">
                            <br />
                            <input type="hidden" name="id" value={values.id} />
                            <button type="submit" className={"icon button submit-btn " + (isSubmitting ? "isSubmitting" : "")} disabled={isSubmitting} >
                              ذخیره
            </button>
                          </div>

                        </div>
                      </div>


                    </form>
                  </div>
                </div>

              );
            }}
          </Formik>
        </Modal>

        <MySweetAlert
          show={this.state.confirmDeleteShow}
          onCancel={this.handleSweetAlertHide}
          onConfirm={this.handleConfirmDelete}
          title={"آیا از حذف دلیل [" + this.state.model.name + "] مطمئن هستید؟"}
        >
        </MySweetAlert>
        {this.state.loading ? <Loading /> : ''}
      </div>
    );
  }
}

export default DenyReasons;
