import React, { useState, useEffect } from 'react'
import { toastr } from 'react-redux-toastr'
import NumberFormat from 'react-number-format'
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Dialog from '@material-ui/core/Dialog';
import Divider from '@material-ui/core/Divider';
import DoneAll from '@material-ui/icons/DoneAll';
import Calendar from '../../Shared/Calendar'
import { servicesService } from '../../../_services/services.service'

import { default as ServiceItem } from '../services/Item'

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    direction: 'rtl',
    textAlign: 'right',

  },
  inline: {
    display: 'inline',
  },
}));

function NumberFormatCustom(props) {
  const { inputRef, onChange, ...other } = props;

  return (
    <NumberFormat
      {...other}
      getInputRef={inputRef}
      onValueChange={values => {
        onChange({
          target: {
            value: values.value,
          },
        });
      }}
      thousandSeparator
      isNumericString
    // prefix="$"
    />
  );
}

NumberFormatCustom.propTypes = {
  inputRef: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
};

const DelayServicesModal = (props) => {
  const { data, onClose, open, ...other } = props;
  const [parts, setParts] = useState(props.data ? data.parts : [{ name: '', qty: 1, price: '', totalPrice: '' }])
  const classes = useStyles();
  useEffect(() => {
    props.data && setParts(props.data)
  }, [props.data])

  const onChange = (val) => {

    let dto = {};
    dto["service_id"] = parseInt(data.id);
    dto["service_date"] = val;

    console.log(dto);


    servicesService.delayService(JSON.stringify(dto))
      .then((res) => {

        console.log(res.data);

        props.onCloseAndReload(res.data)
        // history.push('/installer/projects')
        toastr.success('تاریخ سرویس با موفقیت تغییر یافت');
        // history.push("/admin/cities");
      })
      .catch((error) => {
        console.log(error);
        onClose();
      });

  }

  const handleClose = () => {
    onClose()
  };

  return (

    <Dialog
      fullWidth={true}
      onClose={handleClose}
      // onEntering={handleEntering}
      aria-labelledby="confirmation-dialog-title"
      open={open}
      {...other}
    >
      <DialogTitle id="confirmation-dialog-title">تغییر تاریخ سرویس</DialogTitle>
      <DialogContent dividers>
      <Calendar name="start_date"
                defaultValue={data.start_date}
                className="text-input"
                onChange={(val) => {
                  onChange(val);
                }} />
      </DialogContent>
      <DialogActions>
        <Button onClick={() => handleClose()} variant="contained" color="primary" size="large">
          بستن
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default DelayServicesModal;