import React, { useState, useEffect } from 'react'
import Modal from 'react-responsive-modal'
import ReactTable from "react-table"
import Loading from '../../../Shared/Loading'
import UserSelect from '../../../Shared/UserSelect'
import { Formik,  ErrorMessage, getIn } from 'formik'
import * as Yup from 'yup'
import ErrorPopUp from '../../../Shared/ErrorPopUp'
import { FiEdit } from 'react-icons/fi'
import { dashboardService } from '../../../../_services/dashboard.service'

const Tab2 = () => {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [modalShow, setModalShow] = useState(false);
  const [model, setModel] = useState({});

  useEffect(() => {

    getData()

  }, [])

  const getData = async () => {

    setLoading(true)

    dashboardService.denied()
      .then((res) => {

        console.log(res.data);

        setData(res.data);

        setLoading(false);

      })
      .catch((error) => {

        console.log(error);

        setLoading(false);

      });

  }

  const handleModalShow = (val) => {

    setModalShow(val)

  }

  const handleModalEdit = (e) => {
    let _model = {};

    _model["deny_service_id"] = data[e].id;

    console.log(_model);

    setModel(_model)

    handleModalShow(true)
  }

  const handleSubmit = (values) => {

    console.log(JSON.stringify(values));

    setLoading(true)

    dashboardService.assign_user(JSON.stringify(values))
      .then((res) => {

        console.log(res.data);

        getData()

        handleModalShow(false)

      })
      .catch((error) => {

        console.log(error);

        setLoading(false)

        handleModalShow(false)

      });

  }

  const ValidationSchema = Yup.object().shape({
    technician_id: Yup.string().trim().required('انتخاب سرویس کار الزامی است'),
  });

  return (
    <React.Fragment>
      <div>
        <div className="row table-container" style={{ marginTop: 0 }}>
          <ReactTable
            data={data}
            filterable

            previousText='صفحه قبل'
            nextText='صفحه بعد'
            loadingText='در حال بارگذاری ...'
            noDataText='نتیجه ای یافت نشد'
            pageText='صفحه'
            ofText='از'
            rowsText='تعداد نمایش'
            getTableProps={() => ({ style: { display: 'block' } })} // direction: rtl fix

            defaultFilterMethod={(filter, row) =>
              String(row[filter.id]) === filter.value}
            columns={[
              {
                Header: "ردیف",
                id: "row",
                maxWidth: 50,
                filterable: false,
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                className: "numberFa",
                Cell: (row) => {
                  return row.index + 1;
                }
              },
              {
                Header: "شماره قرارداد",
                accessor: "contract_id",
                id: "contract_id",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterable: false,
                Cell: row => (
                  row.original.service.contract.contract_number
                )
              },
              {
                Header: "تاریخ سرویس",
                accessor: "service_date",
                id: "service_date",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterable: false,
                Cell: row => (
                  row.original.created_at
                )
              },
              {
                Header: "طرف قرارداد",
                accessor: "customer_fullName",
                id: "customer_fullName",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterable: false,
                Cell: row => (
                  row.original.service.contract.customer.full_name
                )
              },
              {
                Header: "نام سرویس کار",
                accessor: "user_fullName",
                id: "user_fullName",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterable: false,
                Cell: row => (
                  row.original.service.contract.user.full_name
                )
              },
              {
                Header: "دلیل رد سرویس",
                accessor: "deny_reason",
                id: "deny_reason",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterable: false,
                Cell: row => (
                  row.original.deny_reason.name
                )
              },
              {
                Header: "ارجاع سرویس",
                accessor: 'id',
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                sortable: false,
                filterable: false,

                Cell: row => (
                  <div>
                    <a onClick={() => handleModalEdit(row.index)} className="btn btn-sm btn-simple btn-success bmd-ripple bmd-flat-btn" rel="tooltip" data-original-title="ویرایش"><FiEdit size="24px" /></a>

                    {/* <a onClick={() => this.handleDelete(row.index)} className="btn btn-sm btn-simple btn-danger bmd-ripple bmd-flat-btn delete-btn" data-original-title="حذف" rel="tooltip"><FiTrash2 size="24px" /></a> */}
                  </div>
                )
              }
            ]}
            defaultPageSize={10}
            className="-striped -highlight"
          />

        </div>
        <Modal open={modalShow} onClose={() => handleModalShow(false)} center
          classNames={{
            overlay: "customOverlay",
            modal: "customModal",
            closeButton: "customModalCloseButton",
          }}>

          <Formik
            initialValues={model}
            onSubmit={(values, { setSubmitting }) => {
              // console.log(values);
              handleSubmit(values)
            }}
            validationSchema={ValidationSchema}
          >
            {props => {
              const {
                values,
                touched,
                errors,
                isSubmitting,
                handleChange,
                handleBlur,
                handleSubmit,
                setFieldValue,
              } = props;
              return (

                <div className="row" style={{ position: "relative" }}>
                  <div className="col-md-12">
                    <form onSubmit={handleSubmit}>

                      <div className="row">
                        <div className="form-inline">

                          <div className="col-md-12">
                            <label>انتخاب سرویس کار</label>
                            <UserSelect
                              defaultValue={values.technician_id}
                              name="technician_id"
                              onChange={(val) => {
                                // console.log(val);

                                setFieldValue('technician_id', val)
                              }}

                              className={getIn(errors, "technician_id") ? "semantic-select-box has-error" : "semantic-select-box"}
                            />
                            <ErrorMessage component={ErrorPopUp} name="technician_id" />
                          </div>

                          <div className="col-md-12">
                            <br />
                            <input type="hidden" name="id" value={values.id} />
                            <button type="submit" className={"icon button submit-btn " + (isSubmitting ? "isSubmitting" : "")} disabled={isSubmitting} >
                              ذخیره
            </button>
                          </div>

                        </div>
                      </div>


                    </form>
                  </div>
                </div>

              );
            }}
          </Formik>
        </Modal>

        {/* <MySweetAlert
          show={this.state.confirmDeleteShow}
          onCancel={this.handleSweetAlertHide}
          onConfirm={this.handleConfirmDelete}
          title={"آیا از حذف شرکت استاندارد [" + this.state.model.name + "] مطمئن هستید؟"}
        >
        </MySweetAlert> */}
        {loading ? <Loading /> : ''}
      </div>
    </React.Fragment>
  )
}
export default Tab2;