import React from 'react'
import { Router, Route, Redirect, Switch } from 'react-router-dom'
import AdminLayout from '../_components/admin/layout/Layout'
import CustomerLayout from '../_components/customer/dashboard/CustomerLayout'
import TechnicianLayout from '../_components/technician/dashboard/TechnicianLayout'
import MobileLayout from '../_components/Shared/Layout/MobileLayout'
import { history } from '../_helpers'
import { connect } from 'react-redux'

// import Home from '../containers/home'

// import { PrivateRoute } from '../_components'
// import { PrivateRoute } from '../_components'
import { Login } from '../_components/login/index';

import Splash from '../_components/Splash/index';

import AdminDashboard from '../_components/admin/dashboard/adminDashboard/AdminDashboard';

import { Messages as AdminMessages } from '../_components/admin/Messages/index';

import Contracts from '../_components/admin/contracts/index';
import ContractForm from '../_components/admin/contracts/Form';

import Users from '../_components/admin/Users/Users';
import UserForm from '../_components/admin/Users/Form';

import Standards from '../_components/admin/settings/Standards';
import Insurances from '../_components/admin/settings/Insurances';
import Reasons from '../_components/admin/settings/Reasons';
import DenyReasons from '../_components/admin/settings/DenyReasons';

import Technician from '../_components/technician/dashboard/index';
import { Contracts as TechnicianContracts } from '../_components/technician/contracts/index';
import { Services as TechnicianContractServices } from '../_components/technician/services/index';
import { ServiceInfo as TechnicianServiceInfo } from '../_components/technician/services/ServiceInfo';
import { Charts as TechnicianCharts } from '../_components/technician/charts/index';

import Customer from '../_components/customer/dashboard/index';
import Transactions from '../_components/customer/Transactions/index';
import { Contracts as CustomerContracts } from '../_components/customer/contracts/index';
import { Services as CustomerContractServices } from '../_components/customer/services/index';
import { Charts as CustomerCharts } from '../_components/customer/charts/index';

import resetPassword from '../_components/login/resetPassword';

// import AnimatedSwitch from "./AnimatedSwitch";

import Messages from '../_components/Messages/index';


export const MobileRoute = ({ component: Component, user, ...rest }) => (
  <Route {...rest} render={props => (
    <MobileLayout><Component {...props} /></MobileLayout>
  )} />
)

export const AdminRoute = ({ component: Component, user, ...rest }) => (
  <Route {...rest} render={props => (
    user && user.role !== undefined && user.role === 0
      ? <AdminLayout><Component {...props} /></AdminLayout>
      : <Redirect to={{ pathname: '/login', state: { from: props.location } }} />
  )} />
)

export const TechnicianRoute = ({ component: Component, user, ...rest }) => (
  <Route {...rest} render={props => (
    user && user.role !== undefined && user.role === 1 && user.status !== undefined && user.status === 0
      ? <TechnicianLayout><Component {...props} /></TechnicianLayout>
      : <Redirect to={{ pathname: '/login', state: { from: props.location } }} />
  )} />
)

export const CustomerRoute = ({ component: Component, user, ...rest }) => (
  <Route {...rest} render={props => (
    user && user.role !== undefined && user.role === 2 && user.status !== undefined && user.status === 0
      ? <CustomerLayout><Component {...props} /></CustomerLayout>
      : <Redirect to={{ pathname: '/login', state: { from: props.location } }} />
  )} />
)

class App extends React.Component {
  render() {
    const { user } = this.props;
    return (
      <Router history={history}>
        <Switch>

          {user && user.role !== undefined && user.role === 0 ?
            <AdminRoute user={user} exact path='/' component={AdminDashboard} />
            :
            <Route user={user} exact path='/' component={Splash} />
          }
          <Route user={user} exact path='/Splash' component={Splash} />
          <Route path="/login" exact component={Login} />

          <AdminRoute user={user} exact path='/admin' component={AdminDashboard} />
          <AdminRoute user={user} exact path="/admin/Messages" component={AdminMessages} />

          <AdminRoute user={user} exact path="/admin/contracts" component={Contracts} />
          <AdminRoute user={user} exact path="/admin/contracts/new" component={ContractForm} />
          <AdminRoute user={user} exact path="/admin/contracts/:id/edit" component={ContractForm} />

          <AdminRoute user={user} exact path="/admin/users" component={Users} />
          <AdminRoute user={user} exact path="/admin/users/new" component={UserForm} />
          <AdminRoute user={user} exact path="/admin/users/:id/edit" component={UserForm} />

          <AdminRoute user={user} exact path="/admin/Settings/Standards" component={Standards} />
          <AdminRoute user={user} exact path="/admin/Settings/Insurances" component={Insurances} />
          <AdminRoute user={user} exact path="/admin/Settings/Reasons" component={Reasons} />
          <AdminRoute user={user} exact path="/admin/Settings/DenyReasons" component={DenyReasons} />





          <TechnicianRoute user={user} path="/Technician" exact component={Technician} />
          <TechnicianRoute user={user} path="/Technician/messages" exact component={Messages} />
          <TechnicianRoute user={user} path="/Technician/contracts" exact component={TechnicianContracts} />
          <TechnicianRoute user={user} path="/Technician/charts" exact component={TechnicianCharts} />
          <TechnicianRoute user={user} path="/Technician/services/:id" exact component={TechnicianContractServices} />
          <TechnicianRoute user={user} path="/Technician/services" exact component={TechnicianContractServices} />
          <TechnicianRoute user={user} path="/Technician/services/:id/serviceInfo/:sid" exact component={TechnicianServiceInfo} />

          {user && user.role !== undefined && user.role === 1 && user.status !== undefined && user.status === 0 ?
            <TechnicianRoute user={user} path="/Login/ResetPassword" exact component={resetPassword} />
            : user && user.role !== undefined && user.role === 2 && user.status !== undefined && user.status === 0 ?
              <CustomerRoute user={user} path="/Login/ResetPassword" exact component={resetPassword} />
              : ''}

          <CustomerRoute user={user} path="/Customer" exact component={Customer} />
          <CustomerRoute user={user} path="/Customer/messages" exact component={Messages} />
          <CustomerRoute user={user} path="/Customer/contracts" exact component={CustomerContracts} />
          <CustomerRoute user={user} path="/Customer/charts" exact component={CustomerCharts} />
          <CustomerRoute user={user} path="/Customer/services/:id" exact component={CustomerContractServices} />
          <CustomerRoute user={user} path="/Customer/Transactions" exact component={Transactions} />


          <CustomerRoute user={user} path="/messages" exact component={Messages} />


        </Switch>
      </Router>

    );
  }
}

function mapStateToProps(state) {
  const { authentication } = state;
  const { user } = authentication;
  // console.log(user.role);
  return {
    user
  };
}

const connectedApp = connect(mapStateToProps)(App);
export { connectedApp as App };

