import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText';
import { history } from '../../../_helpers'
import { FaHashtag } from 'react-icons/fa'
import { FaPhone } from 'react-icons/fa'


const useStyles = makeStyles(theme => ({
  listItem: {
    margin: 0,
    padding: 10,
    display: 'block',
  },
  listItemDetails: {
    direction: 'rtl',
    textAlign: 'right',
    display: 'block',
  },
  listItemDetailsSpan: {
    display: 'block',
    width: '100%'
  },
}))

const ProjectItem = ({ data, onClickItem }) => {


  const classes = useStyles()

  return (
    data ?
      <div onClick={() => history.push("/customer/services/" + data.id)} >
        <div className="first-row">
          <span>
            <FaHashtag size="16px" />
            <span>{data.contract_number}</span>
          </span>
          <span> </span>
          <span>
            {/* <GoCalendar size="16px" />
                        <span>{data.created_at}</span> */}
          </span>
        </div>
        <ListItem button className={classes.listItem}
        // onClick={() => { onClickItem(data.id) }}
        >

          <ListItemText
            className="message-title rtl"
            primary={"آدرس: " + data.address}
            style={{ display: 'block', width: '100%' }}
          />
          <hr />
          <div className={classes.listItemDetails}>
            <div className="row">
              <div className="col-xs-6">
                <span className={classes.listItemDetailsSpan}>{"تعداد طبقات: " + data.floors}</span>
              </div>
              <div className="col-xs-6 text-left">
                <span className={classes.listItemDetailsSpan}>{"تعداد توقف: " + data.stops}</span>
              </div>
            </div>
            <br />
            <div className="row">
              <div className="col-xs-6">
                <span className={classes.listItemDetailsSpan}>{"استاندارد: " + (data.standard_id ? 'دارد' : 'ندارد')}</span>
              </div>
              <div className="col-xs-6 text-left">
                <span className={classes.listItemDetailsSpan}>{"تاریخ اتمام: " + data.standard_finish_date}</span>
              </div>
            </div>
            <br />
            <div className="row">
              <div className="col-xs-6">
                <span className={classes.listItemDetailsSpan}>{"بیمه: " + (data.insurance_id ? 'دارد' : 'ندارد')}</span>
              </div>
              <div className="col-xs-6 text-left">
                <span className={classes.listItemDetailsSpan}>{"تاریخ اتمام: " + data.insurance_finish_date}</span>
              </div>
            </div>
            <hr />
            <div className="row">
              <div className="col-xs-12">
                <span className={classes.listItemDetailsSpan}>{"سرویس کار: " + data.user.full_name}</span>
              </div>
            </div>
            <br />
            <div className="row">
              <div className="col-xs-12">
                <span className={classes.listItemDetailsSpan}><FaPhone />{data.user.cell}</span>
              </div>
            </div>

          </div>
        </ListItem>
        <div className="last-row">
          <span className="ltr">از: {data.start_date}</span>
          <span className="rtl">تا: {data.finish_date}</span>
        </div>
        {/* <Collapse in={data.open} timeout="auto" unmountOnExit>
      <List component="div" disablePadding>
        <ListItem button className={classes.nested}>
          test
        </ListItem>
      </List>
    </Collapse> */}
      </div>
      : ''
  )
}

export default ProjectItem