/*eslint-env jquery*/
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import MySweetAlert from '../../../Shared/MySweetAlert'
import Loading from '../../../Shared/Loading'
import { dashboardService } from '../../../../_services/dashboard.service'
import { FiEdit } from 'react-icons/fi'
import DashboardContractsTypeSelect from '../partials/DashboardContractsTypeSelect'
import matchSorter from 'match-sorter'
import ReactTable from "react-table"


class Tab5 extends Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      initData: [],
      loading: true,
      confirmDeleteShow: false,
      requiredItem: 0,
      deleteItemIndex: 0,
      model: {},
      contractsType: 'due_contracts'
    }
  }

  componentDidMount() {
    // this.getData()
  }

  handleSweetAlertHide = () => {
    this.setState({ confirmDeleteShow: false });
  }

  getData() {
    const { contractsType } = this.state;
    dashboardService.getContracts()
      .then((res) => {
        
        let initData = res.data;

        let type = `${contractsType}`

        let data = initData[type] ? initData[type][0] : []
    
        console.log(type, data);

        this.setState({ initData: initData, data: data, loading: false });
      })
      .catch((error) => {
        console.log(error);
        this.setState({ loading: false });
      })
  };

  handleError(error) {
    this.setState({ loading: false });
    // // toastr.warning('مشکلی پیش آمد', error);
  }

  handleContractsTypeSelectChange = (val) => {

    const { initData } = this.state;

    const contractsType = val;

    let type = `${contractsType}`

    let data = initData[type] ? initData[type][0] : []

    console.log(type, data);

    this.setState({ data: data, contractsType: val });

  }

  render() {
    const { data, contractsType } = this.state;
    return (
      <div>
        <div className="row">
          <div className="col-lg-6">
            <fieldset className="fieldset">
              <legend>انتخاب فیلتر</legend>
              <DashboardContractsTypeSelect
                defaultValue={contractsType}
                onChange={(val) => this.handleContractsTypeSelectChange(val.value)}
              />
            </fieldset>
          </div>
        </div>
        <div className="row table-container" style={{ marginTop: 0, display: data && data.length > 0 ? 'block' : 'block' }} >
          <div className="row table-container" style={{ marginTop: 0 }}>
            <ReactTable
              data={data}
              filterable

              previousText='صفحه قبل'
              nextText='صفحه بعد'
              loadingText='در حال بارگذاری ...'
              noDataText='نتیجه ای یافت نشد'
              pageText='صفحه'
              ofText='از'
              rowsText='تعداد نمایش'
              getTableProps={() => ({ style: { display: 'block' } })} // direction: rtl fix

              defaultFilterMethod={(filter, row) =>
                String(row[filter.id]) === filter.value}
              columns={[
                {
                  Header: "ردیف",
                  id: "row",
                  maxWidth: 50,
                  filterable: false,
                  style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                  className: "numberFa",
                  Cell: (row) => {
                    return row.index + 1;
                  }
                },
                // {
                //   Header: 'عکس قرارداد',
                //   accessor: 'photoName',
                //   id: "photoName",
                //   width: 100,
                //   sortable: false,
                //   filterable: false,
                //   style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                //   Cell: row => (
                //     row.original.images && row.original.images.length > 0 ?
                //       <img style={{ height: 70, width: 80, display: 'inline' }} alt='' src={config.uploadsUrl + row.original.images[0]} />
                //       : ''
                //   ),
                // },
                {
                  Header: "نام قرارداد",
                  accessor: "title",
                  id: "title",
                  style: { display: 'flex', alignItems: 'center', overflow: 'visible', justifyContent: 'center' },
                  filterMethod: (filter, rows) =>
                    matchSorter(rows, filter.value, { keys: ["extraCode"] }),
                  filterAll: true,
                  Filter: ({ filter, onChange }) =>
                    <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,
                },
                {
                  Header: "شماره قرارداد",
                  accessor: "contract_number",
                  id: "contract_number",
                  style: { display: 'flex', alignItems: 'center', overflow: 'visible', justifyContent: 'center' },
                  filterMethod: (filter, rows) =>
                    matchSorter(rows, filter.value, { keys: ["extraCode"] }),
                  filterAll: true,
                  Filter: ({ filter, onChange }) =>
                    <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,
                },
                {
                  Header: "تاریخ اتمام",
                  id: "total_price",
                  accessor: d => d.finish_date,
                  style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                  minWidth: 120,
                  className: "numberFa",
                  filterAll: true,
                  filterMethod: (filter, rows) =>
                    matchSorter(rows, filter.value, { keys: ["finish_date"] }),
                  Filter: ({ filter, onChange }) =>
                    <input placeholder="" allowClear onChange={event => onChange(event.target.value)} />,
                },
                {
                  Header: "ویرایش",
                  accessor: 'id',
                  style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                  sortable: false,
                  Cell: row => (
                    <div>
                      <Link to={'/admin/contracts/' + row.original.id + '/edit'} params={{ id: row.original.id }} className="btn btn-sm btn-simple btn-success bmd-ripple bmd-flat-btn" rel="tooltip" data-original-title="ویرایش"><FiEdit size="24px" /></Link>
                    </div>
                  )
                }
              ]}
              defaultPageSize={10}
              className="-striped -highlight"
            />

          </div>

          <MySweetAlert
            show={this.state.confirmDeleteShow}
            onCancel={this.handleSweetAlertHide}
            onConfirm={this.handleConfirmDelete}
            title={"آیا از حذف قرارداد  [" + this.state.model.firstName + ' ' + this.state.model.lastName + "] مطمئن هستید؟"}
          >
          </MySweetAlert>

          {this.state.loading ? <Loading /> : ''}
        </div>
      </div>
    );
  }
}

export default Tab5;
