import React, { useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import ListItem from '@material-ui/core/ListItem'
import Divider from '@material-ui/core/Divider';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import { GoCalendar } from 'react-icons/go'
import { FaHashtag } from 'react-icons/fa'
import technician from '../../../assets/images/technician.png'
import { FaPhone } from 'react-icons/fa'
import DelayServicesModal from './DelayServicesModal'
import { AiFillPropertySafety } from 'react-icons/ai';


const useStyles = makeStyles(theme => ({
  listItem: {
    margin: 0,
    padding: 10,
  },
}))

const ServiceItem = (props) => {

  const [data, setData] = React.useState(props.data)
  const [delayServiceDialogShow, setDelayServiceDialogShow] = React.useState(false)
  const [delayServiceData, setDelayServiceData] = React.useState({})

  useEffect(() => {
    setData(props.data)
  }, [])

  const classes = useStyles()

  const handleShowDealyServiceDialog = () => {

    setDelayServiceData(data)

    setDelayServiceDialogShow(true)

  }

  const handleCloseDelayServiceDialog = () => {

    setDelayServiceDialogShow(false)

  }

  const onCloseAndReload = (val) => {

    setData({...val})

    setDelayServiceDialogShow(false)

  }

  return (
    data &&
    <div>
      <div className="order-details">
        <div className="first-row">
          <span>
            <FaHashtag size="16px" />
            <span>{data && data.contract && data.contract.contract_number}</span>
          </span>
          <span> </span>
          <span>
            <GoCalendar size="16px" />
            <span>{data.service_date}</span>

          </span>
        </div>
        <ListItem button className={classes.listItem} onClick={() => {
          props.onItemClick(data)
          // console.log(data);
          // let newData = data;

          // newData["open"] = data.open ? !data.open : true
          // setData(newData)
          // setTotal(newData.length)

        }}
        >
          <Divider />
          <ListItemAvatar>
            <Avatar alt="Remy Sharp" src={technician} className={classes.bigAvatar} />
          </ListItemAvatar>
          <ListItemText
            className="order-details"
            primary={data.user.full_name}
            secondary={<span><FaPhone />{data.user.cell}</span>}
          />
          {/* {data.open ? <ExpandLess /> : <ExpandMore />} */}
        </ListItem>
        <div className="last-row">
          <span>نوع سرویس: {data.request_type === 0 ? 'ماهانه' : 'خرابی'}</span>
          <span>
            {data.request_type === 0 &&
              (<a className="delay-service-btn" onClick={() => handleShowDealyServiceDialog()}>تغییر تاریخ سرویس</a>)
            }
          </span>
        </div>
        {/* <Collapse in={data.open} timeout="auto" unmountOnExit>
      <List component="div" disablePadding>
        <ListItem button className={classes.nested}>
          test
        </ListItem>
      </List>
    </Collapse> */}
      </div>
      <DelayServicesModal
        data={delayServiceData}
        open={delayServiceDialogShow}
        onClose={() => handleCloseDelayServiceDialog()}
        onCloseAndReload={val => onCloseAndReload(val)}
      />
    </div>
  )
}

export default ServiceItem