import React, { useState, useEffect } from 'react'
import NumberFormat from 'react-number-format'
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Dialog from '@material-ui/core/Dialog';
import Input from '@material-ui/core/Input';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';

function NumberFormatCustom(props) {
  const { inputRef, onChange, ...other } = props;

  return (
    <NumberFormat
      {...other}
      getInputRef={inputRef}
      onValueChange={values => {
        onChange({
          target: {
            value: values.value,
          },
        });
      }}
      thousandSeparator
      isNumericString
    // prefix="$"
    />
  );
}

NumberFormatCustom.propTypes = {
  inputRef: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
};

const CreateFactorModal = (props) => {
  const { data, onClose, open, ...other } = props;
  const [parts, setParts] = useState(data || [{ name: '', qty: 1, price: '', totalPrice: '' }])

  useEffect(() => {
    setParts(props.data)
  }, [props.data])

  const handleChange = (name, index) => event => {

    var newParts = [...parts];
    var newPart = parts[index];
    newPart[name] = event.target.value;
    newPart["totalPrice"] = newPart.qty * newPart.price;

    newParts[index] = newPart;
    // console.log(newParts);

    setParts(newParts)

  };

  const handleAddRow = () => {

    var newParts = [...parts];
    var newPart = { name: '', qty: 1, price: '', totalPrice: '' };
    newParts.push(newPart);
    setParts(newParts);

  }

  const handleClose = () => {
    onClose()
  };

  const handleChangeAndClose = () => {

    props.onChangeAndClose(parts)

  }

  return (
    <Dialog
      fullScreen={true}
      onClose={handleClose}
      // onEntering={handleEntering}
      aria-labelledby="confirmation-dialog-title"
      open={open}
      {...other}
    >
      <DialogTitle id="confirmation-dialog-title">ثبت دلیل خرابی</DialogTitle>
      <DialogContent dividers>

        <table className="table-parts" style={{ width: '100%' }}>
          <thead>
            <tr>
              <th>ردیف</th>
              <th>نام کالا / خدمات</th>
              <th>تعداد</th>
              <th>فی</th>
              <th>جمع</th>
            </tr>
          </thead>
          <tbody>
            {
              parts.map((part, index) => (
                <tr key={"part-" + index}>
                  <td>{index + 1}</td>
                  <td>
                    <Input
                      className="text-center number-fa"
                      onChange={handleChange('name', index)}
                      value={part.name}
                    />
                  </td>
                  <td>
                    <Input
                      type="number"
                      className="text-center number-fa ltr"
                      // inputProps={{
                      //   inputComponent: NumberFormatCustom,
                      // }}
                      onChange={handleChange('qty', index)}
                      value={part.qty}
                    />
                  </td>
                  <td>
                    <Input
                      type="number"
                      className="text-center number-fa ltr"
                      // inputProps={{
                      //   inputComponent: NumberFormatCustom,
                      // }}
                      onChange={handleChange('price', index)}
                      value={part.price}
                    />
                  </td>
                  <td>
                    <Input
                      type="number"
                      className="text-center number-fa ltr"
                      disabled
                      // inputProps={{
                      //   inputComponent: NumberFormatCustom,
                      // }}
                      value={part.totalPrice}
                    />
                  </td>
                </tr>
              ))
            }
          </tbody>
        </table>
        <Fab color="primary" aria-label="add" size="small" className="pull-left" onClick={() => handleAddRow()}>
          <AddIcon />
        </Fab>

      </DialogContent>
      <DialogActions>
        <Button onClick={() => handleChangeAndClose()} color="primary">
          بستن
        </Button>
        {/* <Button onClick={() => handleClose()} color="primary">
          لغو
        </Button> */}
      </DialogActions>
    </Dialog>

  )
}

export default CreateFactorModal;